<template>
	<div class="container">
        <PopUp class="popup" v-if="pop_up.openned" :text="pop_up.text" :subtext="pop_up.subtext" :OnConfirm="pop_up.func_confirm" :OnCancel="pop_up.func_cancel" />
        <PopUp2 class="popup" v-if="pop_up2.openned" :text="pop_up2.text" :title="pop_up2.title" :loading="pop_up2.loading" />

		<section class="items">
            <div class="cartTitle">
				<p>{{translate.TR_SELL_STORE}}</p>
			</div>

			<div class="box">
				<div class="img">
					<img src="img/banner.png" alt="">
				</div>
				<div class="text">
					<div class="lineCenter">
						<div>
							<p class="columnText">{{translate.TR_SIMBOL_MONEY}} {{price_sell.toFixed(2)}}</p>
							<p class="columnSubText">{{translate.TR_SELL_TEXT}}</p>
						</div>
					</div>
					<div class="line">
						<div class="inputValue">
							<div class="button">
								<div class="buttonText" @click="ConfirmSell">
									<p>{{translate.TR_SELL}}</p>
									<!-- <i class="fa-solid fa-check"></i> -->
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</section>
	</div>
</template>

<script>
    import axios from "axios";
	// import Loading from "@/components/Loading.vue";
	import PopUp from "@/components/PopUp2.vue";
    import PopUp2 from "@/components/PopUp4.vue";
	export default {
		name: "TemplateOneItems",

        components: {
			// Loading,
			PopUp,
            PopUp2,
		},

		data: function () {
			return {
                id: this.$store.state.openned_store.id,
                price_sell: this.$store.state.openned_store.price_sell,

                pop_up: {
					openned: false,
					text: "",
                    subtext: "",
					func_confirm: false,
					func_cancel: this.PopUpCancel,
				},

                pop_up2: {
					openned: false,
					text: "",
                    title: "",
                    loading: false,
				},

				translate: this.$store.state.translate,
            };
		},

		methods: {
            ConfirmSell: function(){
                if(this.pop_up.openned || this.pop_up2.openned)
                    return;
            
                this.pop_up.openned = true;
                this.pop_up.text = this.translate.TR_SELL_STORE;
                this.pop_up.subtext = this.translate.TR_SELL_CONFIRM;

                this.pop_up.func_confirm = () => {
                    this.SendToClient("SellStore", {
                        id: this.id,
                    });

                    this.PopUpCancel();

                    this.pop_up2.openned = true;
                    this.pop_up2.loading = true;
                    this.pop_up2.title = this.translate.TR_WAIT;
                };
            },

            PopUpCancel: function () {
				this.pop_up.openned = false;
				this.pop_up.text = "";
                this.pop_up.subtext = "";
				this.pop_up.func_confirm = this.PopUpCancel;

                this.pop_up2.openned = false;
				this.pop_up2.text = "";
                this.pop_up2.title = "";
                this.pop_up2.loading = false;
			},

            SendToClient: function (to, data) {
				axios
					.post(`https://${this.$store.state.name_resource}/${to}`, data)
					.catch((msg) =>
						console.log(
							`An error has occurred (TemplateTwoSell): ${to} : ${msg}`
						)
					);
			},

			ReceiveLua: function (event) {
				if (!event || !event.data) return;
				const lua = event.data;
				if (lua.confirm_sell) {
					this.pop_up2.loading = false;
                    this.pop_up2.title = this.translate.TR_OPERATION_SUCCESS;
				}
			},

        },

		created: function () {
			window.addEventListener("message", this.ReceiveLua);
		},
		unmounted: function () {
			window.removeEventListener("message", this.ReceiveLua);
		},
	};
</script>

<style scoped>
	.container {
		background-color: var(--nav_border_selected);
		margin-top: -0.1vh;
		flex: 1;
		padding: 2vh 5vh;

        position: relative;
	}

    .cartTitle {
		text-align: center;
		font-size: 5vh;
		font-weight: bolder;
		color: var(--nav_border);
	}

	.items {
		/* margin-top: 5vh; */
		/* margin-left: 5vh; */
		width: 100%;
		overflow: hidden;
	}

	.items .box {
		width: 111vh;
		/* height: 50vh; */

        margin: auto;

        /* background: black; */
	}

	.img {
		margin: auto;
		width: 15vh;
		height: 15vh;
		/* background: gray; */
		margin-top: 5vh;
	}

	.img img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	.text {
		/* margin-left: 1vh; */
		display: flex;
		flex-direction: column;
	}

	.text .line {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		padding: 0 1vh;
		margin-bottom: 2vh;
		margin-top: 2vh;
	}

	.text .lineCenter {
		display: flex;
		flex-direction: row;
		justify-content: center;
		margin-top: 3vh;
	}

	.text .columnText {
		font-size: 4vh;
        font-weight: 500;
        color: #FB4A59;
        text-shadow: 1px 1px 1px rgb(158, 158, 158);
        margin-top: 5vh;
	}

	.text .columnSubText {
		font-size: 2vh;
		margin-top: 1vh;
        font-size: 2.2vh;
        font-weight: 500;
        color: rgb(68, 68, 68);
	}

	.text .lineCenter .columnSubText,
	.text .lineCenter .columnText {
		text-align: center;
	}

	.text .button {
		color: var(--text_themed);
		font-size: 2vh;

		position: relative;
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: center;

		width: 100%;
		height: 4vh;
	}
	.text .button .buttonText {
		position: absolute;
		display: flex;
		flex-direction: row;
		width: max-content;

		justify-content: center;

		background: var(--themed-color);
		border-radius: 5px;
		padding: 1vh 3vh;
	}
	.text .button .buttonText i {
		margin-left: 1vh;
	}

    .inputValue input{
        background: rgba(255, 255, 255, 0.05);
        border: none;
        outline: none;
        font-size: 2vh;
        padding: 1vh;
        margin: auto;
        text-align: center;
        margin-bottom: 2vh;
        width: 90%;
        color: white;
        border-radius: 5px;
    }

    .inputValue{
        flex: 1;
        margin-top: 5vh;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .popup{
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        z-index: 1;
    }
</style>