<template>
	<div class="container">
        <PopUp v-if="pop_up.openned" :text="pop_up.text" :buttons="pop_up.buttons" :field="pop_up.field" :confirmation="pop_up.confirmation" :loading="pop_up.loading" :error="pop_up.error" :quest="pop_up.quest" :func_confirm="pop_up.func_confirm" :func_cancel="pop_up.func_cancel" />

		<section class="items">
			<div class="box">
				<div class="title">
					<div class="borderTop"></div>
					<div class="bgTitle"></div>
					<p>{{translate.TR_SELL}}</p>
				</div>
				<div class="back" @click="() => $router.back()">
					<i class="fa-solid fa-caret-left"></i>
					<p>{{translate.TR_BACK}}</p>
				</div>

				<div class="img">
					<img src="img/banner.png" alt="">
				</div>
				<div class="text">
					<div class="lineCenter">
						<div>
							<p class="columnText">{{translate.TR_SIMBOL_MONEY}} {{price_sell}},00</p>
							<p class="columnSubText">{{translate.TR_SELL_TEXT}}</p>
						</div>
					</div>
					<div class="line">
						<div class="inputValue">
							<div class="button">
								<div class="buttonText" @click="ConfirmSell">
									<p>{{translate.TR_SELL}}</p>
									<!-- <i class="fa-solid fa-check"></i> -->
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</section>
	</div>
</template>

<script>
    import axios from "axios";
	// import Loading from "@/components/Loading.vue";
	import PopUp from "@/components/PopUp.vue";
	export default {
		name: "TemplateOneItems",

        components: {
			// Loading,
			PopUp,
		},

		data: function () {
			return {
                id: this.$store.state.openned_store.id,
                price_sell: this.$store.state.openned_store.price_sell,

                pop_up: {
					openned: false,
					text: "",
					buttons: true,
					field: false,
					confirmation: false,
					loading: false,
					error: false,
					quest: true,
					func_confirm: false,
					func_cancel: this.PopUpCancel,
				},

				translate: this.$store.state.translate,
            };
		},

		methods: {
            ConfirmSell: function(){
                this.pop_up.openned = true;
                this.pop_up.text = this.translate.TR_SELL_CONFIRM;
                this.pop_up.buttons = true;
                this.pop_up.quest = true;

                this.pop_up.func_confirm = () => {
                    this.pop_up.loading = true;
                    this.pop_up.quest = false;
                    this.pop_up.text = this.translate.TR_WAIT;
                    this.pop_up.buttons = false;

                    this.SendToClient("SellStore", {
                        id: this.id,
                    })
                };
            },

            PopUpCancel: function () {
				this.pop_up.openned = false;
				this.pop_up.text = "";
				this.pop_up.buttons = false;
				this.pop_up.field = false;
				this.pop_up.confirmation = false;
				this.pop_up.loading = false;
				this.pop_up.error = false;
				this.pop_up.quest = false;
				this.pop_up.func_confirm = this.PopUpCancel;
			},

            SendToClient: function (to, data) {
				axios
					.post(`https://${this.$store.state.name_resource}/${to}`, data)
					.catch((msg) =>
						console.log(
							`An error has occurred (TemplateOneSell): ${to} : ${msg}`
						)
					);
			},

			ReceiveLua: function (event) {
				if (!event || !event.data) return;
				const lua = event.data;
				if (lua.confirm_sell) {
					this.pop_up.loading = false;
                    this.pop_up.confirmation = true;
                    this.pop_up.text = "";
				}
			},

        },

		created: function () {
			window.addEventListener("message", this.ReceiveLua);
		},
		unmounted: function () {
			window.removeEventListener("message", this.ReceiveLua);
		},
	};
</script>

<style scoped>
	.container {
		flex: 1;
		width: 150vh;
		margin: auto;
	}

	.items {
		margin-top: 5vh;
		margin-left: 5vh;
		width: 100%;
		overflow: hidden;
	}

	.items .box {
		width: 111vh;
		height: 70vh;

		background: var(--product_background);
		border: 1px solid var(--product_border);

		margin-bottom: 2vh;
	}

	.title {
		position: relative;
	}

	.title .borderTop {
		background: var(--product_border);
		width: 100%;
		height: 0.5vh;
	}

	.title p {
		color: white;
		font-size: 1.3vh;
		padding: 0.5vh;
		padding-left: 1vh;
		text-transform: uppercase;
	}

	.title .bgTitle {
		border-right: 5vh solid transparent;
		border-top: 2.8vh solid var(--product_border);
		width: 20vh;
		height: 0;
		position: absolute;
		top: 0.5vh;
	}

	.back {
		display: flex;
		flex-direction: row;
		align-items: center;
		color: rgba(255, 255, 255, 0.596);
		font-size: 2vh;
		padding: 2vh 1vh;
	}
	.back i {
		margin-right: 1vh;
	}

	.img {
		margin: auto;
		width: 15vh;
		height: 15vh;
		/* background: gray; */
		margin-top: 5vh;
	}

	.img img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	.text {
		/* margin-left: 1vh; */
		display: flex;
		flex-direction: column;
	}

	.text .line {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		padding: 0 1vh;
		margin-bottom: 2vh;
		margin-top: 2vh;
	}

	.text .lineCenter {
		display: flex;
		flex-direction: row;
		justify-content: center;
		margin-top: 3vh;
	}

	.text .columnText {
		font-size: 5vh;
		color: rgb(245, 42, 42);
	}

	.text .columnSubText {
		font-size: 2vh;
		color: rgba(190, 158, 158, 0.534);
	}

	.text .lineCenter .columnSubText,
	.text .lineCenter .columnText {
		text-align: center;
	}

	.text .button {
		color: white;
		font-size: 2vh;

		position: relative;
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: center;

		width: 100%;
		height: 4vh;
	}
	.text .button .buttonText {
		position: absolute;
		display: flex;
		flex-direction: row;
		width: max-content;

		justify-content: center;

		background: rgba(145, 14, 14, 1);
		border-radius: 5px;
		padding: 1vh 3vh;
	}
	.text .button .buttonText i {
		margin-left: 1vh;
	}

    .inputValue input{
        background: rgba(255, 255, 255, 0.05);
        border: none;
        outline: none;
        font-size: 2vh;
        padding: 1vh;
        margin: auto;
        text-align: center;
        margin-bottom: 2vh;
        width: 90%;
        color: white;
        border-radius: 5px;
    }

    .inputValue{
        flex: 1;
        margin-top: 5vh;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
</style>