<template>
	<div class="template">
		<nav class="navigation">
			<p class="close" @click="Close" :style="{color: information.colors.title}">X</p>
			<i class="fas fa-arrow-left preview" @click="ClickBack" :style="{color: information.colors.title}" v-if="this.$route.name != 'itemsTemplate02'" ></i>

			<div class="titleScope">
				<h2 :style="{color: information.colors.title}">{{information.name}}</h2>
			</div>

			<ul class="navButtons">
				<router-link :to="{name:'itemsTemplate02'}">
					<li>
						{{translate.TR_PRODUCTS}}
					</li>
				</router-link>
				<router-link :to="{name:'missionsTemplate02'}">
					<li>
						{{translate.TR_MISSIONS}}
					</li>
				</router-link>
				<router-link :to="{name:'manageTemplate02'}" v-if="(is_admin || is_owner) && has_owner">
					<li>
						{{translate.TR_MANAGE}}
					</li>
				</router-link>
				<router-link :to="{name:'buyTemplate02'}" v-else-if="!has_owner">
					<li>
						{{translate.TR_BUY}}
					</li>
				</router-link>
			</ul>
		</nav>

		<div class="bodyScope">
			<router-view />
		</div>
	</div>
</template>

<script>
	import axios from 'axios';
	export default {
		name: "TemplateTwo",
		data: function () {
			return ({
				is_owner: this.$store.state.openned_store.is_owner,
				is_admin: this.$store.state.openned_store.is_admin,
				has_owner: this.$store.state.openned_store.has_owner,
				information: this.$store.state.openned_store,
				translate: this.$store.state.translate,
			});
		},

		methods: {
			ClickBack: function () {
				this.$router.back();
			},

            SendToClient: function (to, data) {
				axios
					.post(`https://${this.$store.state.name_resource}/${to}`, data)
					.catch((msg) =>
						console.log(`An error has occurred (Template02): ${to} : ${msg}`)
					);
			},
            Close: function () {
				this.SendToClient("ForceClose", {});				
			},
		}
	};
</script>

<style scoped>
	.template {
		width: 150vh;
		height: 85vh;
		overflow: hidden;

		position: absolute;
		top: 50%;
		left: 50%;

		transform: translate(-50%, -50%);

		display: flex;
		flex-direction: column;

		/* background: #03AE74; */
		background: var(--themed-color);
	}

	.navigation {
		width: 100%;
		/* background: rgb(19, 19, 19); */
	}

	.navigation .titleScope {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;

		width: 100%;
		height: 15vh;

		/* background: gray; */
	}

	.titleScope h2 {
		color: white;
		font-size: 4.5vh;
		font-weight: bold;
		text-transform: uppercase;
	}

	ul.navButtons {
		/* background-color: #F1F1F1; */
		margin-left: 10vh;

		display: flex;
		flex-direction: row;
	}

	ul.navButtons li {
		margin-right: 0.5vh;
		background-color: var(--nav_border_selected);

		font-size: 1.6vh;
		font-weight: 600;
		/* color: #323C67; */
		color: var(--nav_border);

		width: max-content;
		padding: 1.5vh 2.5vh;

		border-radius: 5px 5px 0 0;
		transform: translateY(1.2vh);
	}

	ul.navButtons a.router-link-exact-active li {
		font-size: 2.6vh;
		transform: translateY(0vh);
	}

	.bodyScope {
		display: flex;
		flex-direction: column;
		flex: 1;
	}

	.close {
		position: absolute;
		top: 2%;
		right: 1%;
		color: white;
		font-size: 2.5vh;
	}

	.preview {
		position: absolute;
		top: 2%;
		left: 1%;
		color: white;
		font-size: 2.5vh;
	}
</style>