<template>
	<div class="container">
		<section class="items">
			<div class="search">
				<i class="fa-solid fa-magnifying-glass"></i>
				<input type="text" name="" id="" :placeholder="translate.TR_SEARCH_PRODUCT" v-model="search">
			</div>
			<ul class="menu lg-hideScroll">
				<li v-for="(product, index) in FilterProducts()" :key="`product_${index}`">
					<div class="title">
						<div class="borderTop"></div>
						<div class="bgTitle"></div>
						<p>{{product.label}}</p>
					</div>
					<div class="img">
						<img :src="`${url_images}${product.name}.png`" alt="">
					</div>
					<div class="text">
						<div>
							<p class="price">{{translate.TR_SIMBOL_MONEY}} {{product.price}}</p>
							<p class="perunit">{{translate.TR_PER_UNITY}}</p>
						</div>
						<div>
							<div class="cart">
								<div class="buy">
								</div>
								<div class="buytext" @click="AddCart(product)">
									<p>{{translate.TR_ADD_CART}}</p>
									<i class="fa-solid fa-cart-shopping"></i>
								</div>
							</div>
						</div>
					</div>

					<div class="infos">
						<p class="perunit" v-if="weight_system" >{{product.weight}} {{translate.TR_SIMBOL_WEIGHT}}</p>
						<p class="perunit">{{product.amount}} {{translate.TR_UNITY}}</p>
					</div>
				</li>

				<li class="grayScale" v-for="(product, index) in FilterProductsGray()" :key="`product_${index}`">
					<div class="title">
						<div class="borderTop"></div>
						<div class="bgTitle"></div>
						<p>{{product.label}}</p>
					</div>
					<div class="img">
						<img :src="`${url_images}${product.name}.png`" alt="">
					</div>
					<div class="text">
						<div>
							<p class="price">{{translate.TR_SIMBOL_MONEY}} {{product.price}}</p>
							<p class="perunit">{{translate.TR_PER_UNITY}}</p>
						</div>
						<div>
							<div class="cart">
								<div class="buy">
								</div>
								<div class="buytext">
									<p>{{translate.TR_SOLD_OFF}}</p>
									<i class="fa-solid fa-ban"></i>
								</div>
							</div>
						</div>
					</div>
				</li>
			</ul>
		</section>
	</div>
</template>

<script>
	export default {
		name: "TemplateOneItems",

		data: function () {
			return {
				information: this.$store.state.openned_store,
				cart: [],

				search: "",

                weight_system :this.$store.state.weight_system,
                url_images: this.$store.state.url_images,
				translate: this.$store.state.translate,
			};
		},

		methods: {
			AddCart: function (product) {
				product.selected_amount = 1;
				this.cart.push(product);
				this.$store.commit("SetCart", this.cart);
			},
			FilterProducts: function () {
				return this.information.products.filter((product) => {
					return (
						this.cart.filter((product_cart) => product_cart.name == product.name)
							.length == 0 &&
						product.label.toLowerCase().includes(this.search.toLowerCase()) &&
						product.amount > 0
					);
				});
			},
			FilterProductsGray: function () {
				return this.information.products.filter((product) => {
					return (
						this.cart.filter((product_cart) => product_cart.name == product.name)
							.length == 0 &&
						product.label.toLowerCase().includes(this.search.toLowerCase()) &&
						product.amount == 0
					);
				});
			},
		},

		mounted: function () {
			this.cart = this.$store.state.cart;
		},
	};
</script>

<style scoped>
	.container {
		flex: 1;
		width: 150vh;
		margin: auto;
	}

	.items {
		margin-top: 5vh;
		margin-left: 5vh;
		width: 100%;
		overflow: hidden;
	}

	.items .search {
		width: 111vh;
		background: rgba(0, 0, 0, 0.418);
		padding: 1vh;
		border-radius: 5px;
		margin-bottom: 5vh;

		display: flex;
		flex-direction: row;
		align-items: center;
	}

	.items .search i {
		font-size: 2vh;
		color: gray;
		margin-right: 1vh;
	}

	.items .search input {
		flex: 1;
		background: none;
		outline: none;
		border: none;
		font-size: 2vh;
		color: rgb(206, 206, 206);
		text-transform: uppercase;
	}

	.items ul {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		width: 111vh;
	}

	.items ul li {
		width: calc(111vh / 3 - 2vh + 2vh / 3);
		height: 20vh;

		background: var(--product_background);
		border: 1px solid var(--product_border);
		margin-bottom: 2vh;
	}

	.items ul li:not(:nth-child(3n)) {
		margin-right: 2vh;
	}

	.title {
		position: relative;
	}

	.title .borderTop {
		background: var(--product_border);
		width: 100%;
		height: 0.5vh;
	}

	.title p {
		color: var(--product_text);
		font-size: 1.3vh;
		padding: 0.5vh;
		padding-left: 1vh;
		text-transform: uppercase;
	}

	.title .bgTitle {
		border-right: 5vh solid transparent;
		border-top: 2.8vh solid var(--product_border);
		width: 20vh;
		height: 0;
		position: absolute;
		top: 0.5vh;
	}

	.img {
		margin: auto;
		width: 11vh;
		height: 11vh;
		/* background: gray; */
		margin-top: 0.5vh;
	}

	.img img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	.text {
		margin-left: 1vh;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
	}

	.text .price {
		font-size: 2vh;
		color: var(--product_text);
	}

	.text .perunit {
		font-size: 1vh;
		color: var(--product_text);
		opacity: 0.8;
	}

	.text .cart {
		color: var(--text_themed);
		font-size: 2vh;
		margin-right: 2vh;
		position: relative;
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: center;

		width: 15vh;
		height: 4vh;
	}
	.text .cart .buytext {
		position: absolute;
		display: flex;
		flex-direction: row;
		width: 100%;
		justify-content: center;
	}
	.text .cart .buytext i {
		margin-left: 1vh;
	}

	.text .cart .buy {
		position: absolute;
		top: 0%;
		left: 0%;

		-webkit-transform: skew(-15deg);
		-moz-transform: skew(-15deg);
		-o-transform: skew(-15deg);

		background: var(--themed-color);
		width: 100%;
		height: 4vh;
	}

	.menu {
		height: 70vh;
		overflow-y: scroll;
	}

	.menu li {
		position: relative;
	}

	.infos {
		position: absolute;
		top: 10%;
		right: 5%;
		transform: translate(5%, -10%);
	}

	.infos p.perunit {
		color: gray;
		font-size: 1.5vh;
		text-align: right;
	}
</style>