<template>
	<div class="container lg-column">
		<div class="title lg-line lg-alignCenter" @click="() => $router.back()">
			<i class="fa-solid fa-caret-left"></i>
			<h1>{{translate.TR_LIST_STORES}}</h1>
		</div>

		<div class="bodyMenu lg-line">
			<div class="menu lg-column lg-expand">
				<p class="menuTitle">{{translate.TR_SELECT_WICH_TYPE}}</p>

				<ul class="lg-column lg-hideScroll lg-expand">
					<li v-for="(store_type, index) in stores.filter(store => store.is_type == 1)" :key="`stype_${index}`" class="lg-line lg-justifyBetween lg-alignCenter" :class="{selected : selected_type == store_type.id}" @click="SelectType(store_type.id)">
						<p class="lg-uppercase">{{store_type.information.name}}</p>
						<i class="fa-solid fa-chevron-right"></i>
					</li>
				</ul>
			</div>
			<div class="menu lg-column lg-expand">
				<p class="menuTitle">{{translate.TR_SELECT_WICH_STORE}}</p>

				<ul class="lg-column lg-hideScroll lg-expand">
					<li class="lg-line lg-justifyBetween lg-alignCenter" v-for="(store_info, index) in stores.filter(store => store.is_type == 0 && store.information.type == selected_type)" :key="`store_info_${index}`" :class="{selected : selected_store && selected_store.id == store_info.id}" @click="SelectStore(store_info)">
						<div class="menuInfo lg-column">
							<p class="lg-uppercase">{{store_info.information.name}}</p>
						</div>
						<i class="fa-solid fa-chevron-right"></i>
					</li>
				</ul>
			</div>
			<div class="menu lg-column lg-expand2">
				<div class="menuFinal lg-column lg-expand" v-if="selected_store">
					<h2 class="lg-uppercase">{{selected_store.information.name}}</h2>
					<h3>{{selected_store.information.owner_name}}</h3>

					<div class="lg-line lg-justifyBetween">
						<p>{{translate.TR_MONEY}}</p>
						<p>{{selected_store.information.money}}</p>
					</div>

					<div class="lg-line lg-justifyBetween">
						<p>{{translate.TR_PRODUCTS}}</p>
						<p>{{SumAllProducts()}} {{translate.TR_UNITY}}</p>
					</div>

					<div class="lg-line lg-justifyBetween">
						<p>{{translate.TR_MISSIONS}}</p>
						<p>{{selected_store.information.missions.length}}</p>
					</div>

					<div class="lg-line lg-justifyBetween">
						<p>{{translate.TR_PRICE_BUY}}</p>
						<p>{{selected_store.information.price_buy}}</p>
					</div>

					<div class="lg-line lg-justifyBetween">
						<p>{{translate.TR_PRICE_SELL}}</p>
						<p>{{selected_store.information.price_sell}}</p>
					</div>

					<div class="button lg-line lg-justifyBetween" @click="TeleportTo(selected_store.id)">
						<p>{{translate.TR_TELEPORT_TO}}</p>
						<i class="fa-solid fa-chevron-right"></i>
					</div>
					<div class="button lg-line lg-justifyBetween" @click="ManageStore(selected_store.id)">
						<p>{{translate.TR_MANAGE_STORE}}</p>
						<i class="fa-solid fa-chevron-right"></i>
					</div>
					<div class="button lg-line lg-justifyBetween" v-if="!is_price_buy" @click="SetIsPriceBuy(true)">
						<p>{{translate.TR_CHANGE_PRICE_BUY}}</p>
						<i class="fa-solid fa-chevron-right"></i>
					</div>
					<div class="lg-line lg-justifyBetween" v-else>
						<div class="button lg-line lg-justifyBetween lg-alignCenter lg-expand">
							<input type="text" v-model.number="new_price_buy" @input="ValidateInputs" />
							<i class="fa-solid fa-chevron-right"></i>
						</div>

						<div class="button selected lg-line lg-justifyBetween lg-alignCenter lg-expand" @click="ChangePriceBuy(selected_store.id)">
							<p>{{translate.TR_CONFIRM}}</p>
							<i class="fa-solid fa-chevron-right"></i>
						</div>
					</div>
					<div class="button lg-line lg-justifyBetween" v-if="!is_price_sell" @click="SetIsPriceSell(true)">
						<p>{{translate.TR_CHANGE_PRICE_SELL}}</p>
						<i class="fa-solid fa-chevron-right"></i>
					</div>
					<div class="lg-line lg-justifyBetween" v-else>
						<div class="button lg-line lg-justifyBetween lg-alignCenter lg-expand">
							<input type="text" v-model.number="new_price_sell" @input="ValidateInputs" />
							<i class="fa-solid fa-chevron-right"></i>
						</div>

						<div class="button selected lg-line lg-justifyBetween lg-alignCenter lg-expand" @click="ChangePriceSell(selected_store.id)">
							<p>{{translate.TR_CONFIRM}}</p>
							<i class="fa-solid fa-chevron-right"></i>
						</div>
					</div>
					<div class="button lg-line lg-justifyBetween" v-if="!is_deleting" @click="SetIsDeleting(true)">
						<p>{{translate.TR_DELETE}}</p>
						<i class="fa-solid fa-chevron-right"></i>
					</div>

					<div class="lg-line lg-justifyBetween" v-else>
						<div class="button selected lg-line lg-justifyBetween lg-alignCenter lg-expand" @click="DeleteStore(selected_store.id)">
							<p>{{translate.TR_CONFIRM_DELETE}}</p>
							<i class="fa-solid fa-chevron-right"></i>
						</div>

						<div class="button lg-line lg-justifyBetween lg-alignCenter lg-expand" @click="SetIsDeleting(false)">
							<p>{{translate.TR_CANCEL}}</p>
							<i class="fa-solid fa-chevron-right"></i>
						</div>
					</div>

					<div class="button lg-line lg-justifyBetween" v-if="!is_removing" @click="SetIsRemoving(true)">
						<p>{{translate.TR_REMOVE_OWNER}}</p>
						<i class="fa-solid fa-chevron-right"></i>
					</div>

					<div class="lg-line lg-justifyBetween" v-else>
						<div class="button selected lg-line lg-justifyBetween lg-alignCenter lg-expand" @click="RemoveOwner(selected_store.id)">
							<p>{{translate.TR_CONFIRM_REMOVE}}</p>
							<i class="fa-solid fa-chevron-right"></i>
						</div>

						<div class="button lg-line lg-justifyBetween lg-alignCenter lg-expand" @click="SetIsRemoving(false)">
							<p>{{translate.TR_CANCEL}}</p>
							<i class="fa-solid fa-chevron-right"></i>
						</div>
					</div>

				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import axios from "axios";

	export default {
		data: function () {
			return {
				stores: [],
				selected_type: 0,
				selected_store: null,
				is_deleting: false,
				is_removing: false,
				is_price_buy: false,
				is_price_sell: false,
				new_price_buy: 0,
				new_price_sell: 0,
				translate: this.$store.state.translate,
			};
		},
		methods: {
			SelectType: function (store_id) {
				this.selected_type = store_id;
				this.selected_store = null;
			},
			SelectStore: function (store) {
				this.selected_store = store;
				this.SetIsDeleting(false);
				this.SetIsRemoving(false);
                this.SetIsPriceBuy(false);
                this.SetIsPriceSell(false);

                this.new_price_sell = this.selected_store.information.price_sell;
                this.new_price_buy = this.selected_store.information.price_buy;
			},
			SumAllProducts: function () {
				let total = 0;
				this.selected_store.information.products.forEach(
					(product) => (total += product.amount)
				);

				return total;
			},

			SetIsDeleting: function (value) {
				this.is_deleting = value;
			},

			SetIsRemoving: function (value) {
				this.is_removing = value;
			},

			SetIsPriceBuy: function (value) {
				this.is_price_buy = value;
			},

			SetIsPriceSell: function (value) {
				this.is_price_sell = value;
			},

			TeleportTo: function (store_id) {
				this.SendToClient("TeleportTo", {
					store_id: store_id,
				});
			},

			ManageStore: function (store_id) {
				this.SendToClient("ManageStore", {
					store_id: store_id,
				});
			},

			DeleteStore: function (store_id) {
				this.SendToClient("DeleteStore", {
					store_id: store_id,
				});
			},

			RemoveOwner: function (store_id) {
				this.SendToClient("RemoveOwner", {
					store_id: store_id,
				});
			},

			ChangePriceBuy: function (store_id) {
				this.SendToClient("ChangePriceBuy", {
					store_id: store_id,
					price_buy: this.new_price_buy,
				});
				this.SetIsPriceBuy(false);
			},

			ChangePriceSell: function (store_id) {
				this.SendToClient("ChangePriceSell", {
					store_id: store_id,
					price_sell: this.new_price_sell,
				});
				this.SetIsPriceSell(false);
			},

			ValidateInputs: function () {
				if (this.new_price_buy < 0) {
					this.new_price_buy = 0;
				}
				if (this.new_price_sell < 0) {
					this.new_price_sell = 0;
				}

				this.new_price_buy = Math.floor(this.new_price_buy);
				this.new_price_sell = Math.floor(this.new_price_sell);
			},

			Initialize: function () {
				this.SendToClient("LoadStores", {});
			},

			SendToClient: function (to, data) {
				axios
					.post(`https://${this.$store.state.name_resource}/${to}`, data)
					.catch((msg) =>
						console.log(`An error has occurred (SeeStores): ${to} : ${msg}`)
					);
			},

			ReceiveLua: function (event) {
				if (!event || !event.data) return;
				const lua = event.data;
				if (lua.receive_stores) {
					this.stores = lua.stores;

					if (this.stores.length > 0)
						this.selected_type = this.stores.filter(
							(store) => store.is_type == 1
						)[0].id;

					this.selected_store = null;
				}
			},
		},
		created: function () {
			window.addEventListener("message", this.ReceiveLua);
			this.Initialize();
		},
		unmounted: function () {
			window.removeEventListener("message", this.ReceiveLua);
		},
	};
</script>

<style scoped>
	.container {
		width: 111vh;
		margin: auto;
	}

	.title {
		color: rgb(228, 51, 51);
		margin-top: 5vh;
		font-size: 1.8vh;
	}

	.title i {
		font-size: 3vh;
		margin-right: 1vh;
	}

	.bodyMenu {
		margin-top: 5vh;
	}

	.menu:not(:last-child) {
		margin-right: 1vh;
	}

	.menuTitle {
		color: rgba(255, 255, 255, 0.4);
		padding: 0.5vh 0;
	}

    .menu ul{
        max-height: 80vh;
        overflow-y: scroll;
    }

	.menu li {
		background: rgba(255, 255, 255, 0.1);
		padding: 1vh;
		color: white;
		border: 1px solid rgba(128, 128, 128, 0.2);
		margin-bottom: 0.5vh;
		/* transition: 0.3s all; */
	}
	.menu li:hover {
		background: rgba(255, 255, 255, 0.2);
		border: 1px solid rgba(128, 128, 128, 1);
	}

	.menu li.selected {
		background: rgba(241, 0, 0, 0.5);
	}

	.menuInfo div:first-of-type {
		margin-top: 1vh;
	}

	.menuInfo p {
		color: rgba(255, 255, 255, 0.3);
	}

	.menuInfo > p {
		color: white;
	}

	.menuFinal {
		padding-left: 2vh;
	}

	.menuFinal h2 {
		color: rgb(228, 51, 51);
	}

	.menuFinal h3 {
		color: rgb(255, 255, 255);
		font-weight: 500;
		text-transform: uppercase;
	}

	.menuFinal > div {
		margin: 2vh 0;
	}

	.menuFinal div:first-of-type {
		/* margin-top: 8vh; */
	}

	.menuFinal p {
		font-size: 2vh;
		color: rgba(255, 255, 255, 0.795);
	}

	.menuFinal .button {
		background: rgba(255, 255, 255, 0.1);
		padding: 1vh;
		color: white;
		border: 1px solid rgba(128, 128, 128, 0.2);
		margin-bottom: 0.5vh;
	}

	.menuFinal .button:hover {
		background: rgba(255, 255, 255, 0.2);
		border: 1px solid rgba(128, 128, 128, 1);
	}

	.menuFinal .button.selected {
		background: rgba(241, 0, 0, 0.5);
	}

	input {
		padding: 1vh;
		background: rgba(156, 156, 156, 0);
		border: none;
		/* margin-bottom: 1vh; */
		color: rgba(255, 255, 255, 0.616);
		font-size: 1.8vh;
		outline: none;
		/* border-radius: 5px; */
		text-transform: uppercase;
		text-align: center;
	}
</style>