<template>
	<div class="container">
		<section class="items">
			<div class="contentTop">
				<div class="search">
					<i class="fa-solid fa-magnifying-glass"></i>
					<input type="text" name="" id="" :placeholder="translate.TR_SEARCH_PRODUCT" v-model="search">
				</div>

				<router-link :to="{name: 'cartTemplate02'}" class="cartButton">
					<i class="fas fa-shopping-cart"></i>
					<p>{{translate.TR_MY_CART}}</p>
					<i class="fas fa-arrow-right"></i>
				</router-link>
			</div>

			<div class="itemScope lg-hideScroll">
				<ul class="menu lg-hideScroll">
					<li v-for="(product, index) in FilterProducts()" :key="`product_${index}`">
						<p class="itemWeight" v-if="weight_system">{{product.weight}} {{translate.TR_SIMBOL_WEIGHT}}</p>

						<div class="img">
							<img :src="`${url_images}${product.name}.png`" alt="">
						</div>
						<div class="title">
							<p>{{product.label}}</p>
						</div>

						<div class="price">
							<p class="price">{{translate.TR_SIMBOL_MONEY}} {{product.price}}</p>
						</div>

						<div class="itemCartButton" @click="AddCart(product)">
							<i class="fas fa-shopping-cart"></i>
							<p>{{translate.TR_ADD_CART}}</p>
						</div>
					</li>

					<li class="grayScale" v-for="(product, index) in FilterProductsGray()" :key="`product_${index}`">
						<p class="itemWeight" v-if="weight_system">{{product.weight}} {{translate.TR_SIMBOL_WEIGHT}}</p>

						<div class="img">
							<img :src="`${url_images}${product.name}.png`" alt="">
						</div>
						<div class="title">
							<p>{{product.label}}</p>
						</div>

						<div class="price">
							<p class="price">{{translate.TR_SIMBOL_MONEY}} {{product.price}}</p>
						</div>

						<div class="itemCartButton">
							<i class="fas fa-shopping-cart"></i>
							<p>{{translate.TR_ADD_CART}}</p>
						</div>
					</li>
				</ul>
			</div>
		</section>
	</div>
</template>

<script>
	export default {
		name: "TemplateOneItems",

		data: function () {
			return {
				information: this.$store.state.openned_store,
				cart: [],

				search: "",

				weight_system: this.$store.state.weight_system,
				url_images: this.$store.state.url_images,
				translate: this.$store.state.translate,
			};
		},

		methods: {
			AddCart: function (product) {
				product.selected_amount = 1;
				this.cart.push(product);
				this.$store.commit("SetCart", this.cart);
			},
			FilterProducts: function () {
				return this.information.products.filter((product) => {
					return (
						this.cart.filter((product_cart) => product_cart.name == product.name)
							.length == 0 &&
						product.label.toLowerCase().includes(this.search.toLowerCase()) &&
						product.amount > 0
					);
				});
			},
			FilterProductsGray: function () {
				return this.information.products.filter((product) => {
					return (
						this.cart.filter((product_cart) => product_cart.name == product.name)
							.length == 0 &&
						product.label.toLowerCase().includes(this.search.toLowerCase()) &&
						product.amount == 0
					);
				});
			},
		},

		mounted: function () {
			this.cart = this.$store.state.cart;
		},
	};
</script>

<style scoped>
	.container {
		/* background-color: #f1f1f1; */
        background-color: var(--nav_border_selected);
		margin-top: -0.1vh;
		flex: 1;
		padding: 2vh 5vh;
	}

	.items {
		width: 100%;
		overflow: hidden;
	}

	.contentTop {
		display: flex;
		flex-direction: row;

		justify-content: space-between;
		align-items: center;

		margin: 0 5vh;
	}

	.items .search {
		flex: 1;
		display: flex;
		flex-direction: row;
		align-items: center;
		background-color: white;
		color: gray;
		border-radius: 20px;
		padding: 0 2vh;

		font-size: 2.4vh;
	}

	.items .search i {
		font-size: 2vh;
		color: gray;
		margin-right: 1vh;
	}

	.items .search input {
		flex: 1;
		border: none;
		outline: none;
		padding: 1vh;

		font-size: 2.4vh;
		background-color: rgba(255, 255, 255, 0);
		color: gray;
	}

	.cartButton {
		/* background-color: #03b67a; */
        background: var(--themed-color);
		color: var(--text_themed);
		width: max-content;

		padding: 1vh 3vh;
		border-radius: 3px;
		margin-left: 5vh;

		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;

		font-weight: bolder;
		font-size: 3vh;

		position: relative;
	}

	.cartButton:hover {
		opacity: 0.9;
	}
	.cartButton i:first-of-type {
		margin-right: 3vh;
	}

	.cartButton i:last-child {
		margin-left: 3vh;
	}

	.itemScope {
		overflow-y: scroll;
		max-height: 48vh;
		margin-top: 1vh;
	}

	.items ul {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		margin: 0 5vh;
		margin-bottom: 2vh;
	}

	.items ul li {
		/* background-color: white; */
		/* box-shadow: 1px 1px 3px 1px #a2a2a280; */
        background-color: var(--product_background);
        box-shadow: 1px 1px 3px 1px var(--product_border);
		margin-right: 4vh;
		margin-top: 4vh;

		width: 20vh;
		height: 30vh;

		position: relative;
		padding: 0.5vh;
	}

	.title {
		position: relative;
	}

	.title .borderTop {
		background: var(--product_border);
		width: 100%;
		height: 0.5vh;
	}

	.title p {
		/* color: gray; */
        color: var(--product_text);
		text-align: center;
		margin-top: 1vh;
		font-size: 2.2vh;
	}

	.img {
		/* background-color: #e9e9e9; */
        background-color: var(--nav_border_shadow);
		width: 100%;
		height: 55%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}

	.img img {
		object-fit: cover;
		max-width: 50%;
		max-height: 100%;
	}

	.text {
		margin-left: 1vh;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
	}

	.itemWeight {
		position: absolute;
		background-color: #f19d74;
		box-shadow: 3px 3px 6px 1px #bf7e5de0;
		font-size: 1.5vh;
		font-weight: bold;
		color: white;
		padding: 0.6vh 1.5vh;

		transform: translate(-50%, 80%);
	}

	.price {
		/* color: #323c67; */
        color: var(--nav_border);
		font-weight: 500;
		text-align: center;
		margin-top: 1vh;
		font-size: 2.2vh;
	}

	.itemCartButton {
		/* background-color: #03b67a; */
        background: var(--themed-color);
		color: var(--text_themed);

		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;

		font-size: 1.5vh;
		font-weight: bold;

		padding: 1vh 0;

		width: 80%;
		margin: auto;
		margin-top: 1vh;

		position: relative;
	}

	.itemCartButton:hover {
		opacity: 0.9;
	}

	.itemCartButton p {
		margin-left: 1vh;
	}
</style>