<template>
	<div class="container">
        <div v-if="is_loading" class="loading lg-column lg-expand lg-justifyCenter lg-alignCenter">
			<Loading/>
		</div>

		<div v-else-if="confirmation" class="confirmation">
			<svg class="checkmark" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 52 52">
				<circle class="checkmark__circle" cx="26" cy="26" r="25" fill="none" />
				<path class="checkmark__check" fill="none" d="M14.1 27.2l7.1 7.2 16.7-16.8" />
			</svg>
		</div>

        <section class="items" v-else>
			<div class="cartTitle">
				<p>{{translate.TR_STORE_MISSIONS}}</p>
			</div>

			<div class="cartBody">
				<div class="cartItems">
					<p class="cartItemsTitle">{{translate.TR_STORE_PRODUCTS_MISSIONS}}</p>

					<ul class="menu lg-hideScroll">
						<li v-for="(mission, index) in missions" :key="`cart_${index}`">
							<div class="title">
								<div class="img">
									<img :src="`${url_images}${mission.name}.png`" alt="">
								</div>

								<p>{{mission.label}}</p>
							</div>

							<div class="cartAmount">
								<p>x</p>
								<p>{{mission.amount}}</p>
								<!-- <i class="fa-solid fa-cart-shopping"></i> -->
							</div>

							<p class="cartItemprice">{{translate.TR_SIMBOL_MONEY}} {{(mission.payment * mission.amount).toFixed(2)}}</p>

							<p class="cartItemRemove" @click="DeliverMission(mission)">{{translate.TR_DELIVER_MISSION}}</p>
						</li>
					</ul>
				</div>
			</div>
		</section>
	</div>
</template>

<script>
    import axios from 'axios';
    import Loading from "@/components/Loading2.vue";

	export default {
		name: "TemplateOneMissions",

        components: {
			Loading,
		},

        data: function(){
            return({
                id: this.$store.state.openned_store.id,
                missions: this.$store.state.openned_store.missions,

                is_loading: false,
                confirmation: false,

                url_images: this.$store.state.url_images,
                translate: this.$store.state.translate,
            });
        },

        methods:{
            DeliverMission: function(mission){
                this.SendToClient('DeliverMission', {
                    id : this.id,
                    mission : mission,
                });

                this.is_loading = true;
            },

            LoadMissions: function(){
                this.SendToClient("LoadMissions", {
                    id: this.id,
                });

                this.is_loading = true;
            },

            SendToClient: function (to, data) {
				axios
					.post(`https://${this.$store.state.name_resource}/${to}`, data)
					.catch((msg) =>
						console.log(
							`An error has occurred (TemplateTwoMissions): ${to} : ${msg}`
						)
					);
			},

            ReceiveLua: function (event) {
				if (!event || !event.data) return;
				const lua = event.data;
				if (lua.delivered_mission) {
					this.confirmation = true;
                    this.is_loading = false;

                    this.$store.commit("UpdateMissions", lua.missions);
                    this.missions = this.$store.state.openned_store.missions;

                    setTimeout(() => this.confirmation = false, 3000);
				} else if(lua.cancel_mission){
                    this.is_loading = false;

                    this.$store.commit("UpdateMissions", lua.missions);
                    this.missions = this.$store.state.openned_store.missions;
                } else if(lua.load_missions){
                    this.is_loading = false;

                    this.$store.commit("UpdateMissions", lua.missions);
                    this.missions = this.$store.state.openned_store.missions;
                }
			},
        },

        mounted: function(){
            // this.LoadMissions();
        },

        created: function () {
			window.addEventListener("message", this.ReceiveLua);
		},
		unmounted: function () {
			window.removeEventListener("message", this.ReceiveLua);
		},
	};
</script>

<style scoped>
	.container {
		background-color: var(--nav_border_selected);
		margin-top: -0.1vh;
		flex: 1;
		padding: 2vh 5vh;
	}

	.cartTitle {
		text-align: center;
		font-size: 5vh;
		font-weight: bolder;
		color: var(--nav_border);
	}

	.cartBody {
		display: flex;
		flex-direction: row;
		justify-content: center;

		margin-top: 2vh;
	}

	.cartItems {
		background-color: white;
		flex: 1;
		margin-left: 5vh;
		padding: 3vh;
		border-radius: 5px;
		box-shadow: 0px 0px 3px 1px rgba(173, 173, 173, 0.315);
	}

	.cartItemsTitle {
		color: gray;
		font-weight: bold;
		font-size: 2vh;
	}

	.cartItems ul {
		display: flex;
		flex-direction: column;
