		height: 30vh;

		overflow-y: scroll;
	}

	.cartItems li {
		display: flex;
		flex-direction: row;

		align-items: center;
		justify-content: space-between;

		border-bottom: 1px solid rgb(212, 212, 212);
		padding: 1vh 0;

		color: gray;
		font-weight: bold;
		font-size: 2.2vh;
	}

	.cartItems li:first-child {
		border-top: 1px solid rgb(212, 212, 212);
	}

	.title {
		display: flex;
		flex-direction: row;

		align-items: center;
		width: 35%;

		text-overflow: ellipsis;
		overflow: hidden;
		white-space: nowrap;
	}

	.title p {
		margin-left: 1vh;
		overflow: hidden;
		/* background-color: tomato; */
		flex: 1;

		text-overflow: ellipsis;
	}

	.img {
		background-color: #e9e9e9;
		display: flex;
		align-items: center;
		justify-content: center;

		width: 10vh;
		height: 6vh;
	}

	.img img {
		/* width: 100%;
						height: 100%; */
		object-fit: cover;

		max-width: 40%;
		max-height: 100%;
	}

	.cartWeight {
		width: 10%;
		/* background-color: wheat; */
