	}

	#cartBodyFooter {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;

		padding: 1vh 0;
		margin-top: 2vh;
	}

	#cartBodyFooterButton {
		display: flex;
		flex-direction: row;
		align-items: center;

		color: gray;
		font-size: 2vh;

		border: 1px solid gray;
		border-radius: 5px;
