<template>
	<div class="container">
		<section class="items">
			<div class="back" @click="() => $router.back()">
				<i class="fa-solid fa-caret-left"></i>
				<p>{{translate.TR_BACK}}</p>
			</div>
			<ul class="menu lg-hideScroll">
				<li v-for="(option, id) in options" :key="`option_${id}`">
					<div class="title">
						<div class="borderTop"></div>
						<div class="bgTitle"></div>
						<p>{{option.name}}</p>
					</div>
					<div class="img">
						<input type="color" v-model="option.color" @input="ChangeColor(option.element, option.color)">
					</div>
					<div class="text">
						<div class="lineCenter">
							<div>
								<p class="columnText">{{option.text}}</p>
								<p class="columnSubText">{{option.subtext}}</p>
							</div>
						</div>
						<div class="lineCenter">
							<div class="button">
								<div class="buttonText" @click="SaveColors">
									<p>{{translate.TR_SAVE}}</p>
								</div>
							</div>
						</div>
					</div>
				</li>
			</ul>
		</section>
	</div>
</template>

<script>
    import axios from "axios";

	export default {
		name: "TemplateOneColor",

		data: function () {
			return {
                id: this.$store.state.openned_store.id,
				options: [
					{
						name: this.$store.state.translate.TR_TITLE_COLOR,
						text: this.$store.state.translate.TR_TEXT_COLOR,
						subtext: this.$store.state.translate.TR_SUBTEXT_COLOR,
                        element: "title",
                        color: "#000000",
					},
					{
						name: this.$store.state.translate.TR_TITLE_COLOR2,
						text: this.$store.state.translate.TR_TEXT_COLOR2,
						subtext: this.$store.state.translate.TR_SUBTEXT_COLOR2,
                        element: "themed_color",
                        color: "#000000",
					},
					{
						name: this.$store.state.translate.TR_TITLE_COLOR3,
						text: this.$store.state.translate.TR_TEXT_COLOR3,
						subtext: this.$store.state.translate.TR_SUBTEXT_COLOR3,
                        element: "text_themed",
                        color: "#000000",
					},
                    {
						name: this.$store.state.translate.TR_TITLE_COLOR7,
						text: this.$store.state.translate.TR_TEXT_COLOR7,
						subtext: this.$store.state.translate.TR_SUBTEXT_COLOR7,
                        element: "nav_border",
                        color: "#000000",
					},
                    {
						name: this.$store.state.translate.TR_TITLE_COLOR8,
						text: this.$store.state.translate.TR_TEXT_COLOR8,
						subtext: this.$store.state.translate.TR_SUBTEXT_COLOR8,
                        element: "nav_border_selected",
                        color: "#000000",
					},
                    {
						name: this.$store.state.translate.TR_TITLE_COLOR9,
						text: this.$store.state.translate.TR_TEXT_COLOR9,
						subtext: this.$store.state.translate.TR_SUBTEXT_COLOR9,
                        element: "nav_border_shadow",
                        color: "#000000",
					},
					{
						name: this.$store.state.translate.TR_TITLE_COLOR4,
						text: this.$store.state.translate.TR_TEXT_COLOR4,
						subtext: this.$store.state.translate.TR_SUBTEXT_COLOR4,
                        element: "product_background",
                        color: "#000000",
					},
					{
						name: this.$store.state.translate.TR_TITLE_COLOR5,
						text: this.$store.state.translate.TR_TEXT_COLOR5,
						subtext: this.$store.state.translate.TR_SUBTEXT_COLOR5,
                        element: "product_text",
                        color: "#000000",
					},
					{
						name: this.$store.state.translate.TR_TITLE_COLOR6,
						text: this.$store.state.translate.TR_TEXT_COLOR6,
						subtext: this.$store.state.translate.TR_SUBTEXT_COLOR6,
                        element: "product_border",
                        color: "#000000",
					},
				],

                translate: this.$store.state.translate,
			};
		},

		methods: {
            ChangeColor: function(element, color){
                this.$store.commit("UpdateColors", {element, color});
            },
            SaveColors: function(){
                this.SendToClient("SaveColors", {
                    id: this.id,
                    colors: this.$store.state.openned_store.colors,
                })
            },
			SendToClient: function (to, data) {
				axios
					.post(`https://${this.$store.state.name_resource}/${to}`, data)
					.catch((msg) =>
						console.log(
							`An error has occurred (TemplateOneColor): ${to} : ${msg}`
						)
					);
			},
		},

		mounted: function () {
            const colors = this.$store.state.openned_store.colors;
            this.options.forEach(option => {
                option.color = colors[option.element];
            });
        },
	};
</script>

<style scoped>
	.container {
		flex: 1;
		width: 150vh;
		margin: auto;
	}

	.items {
		margin-top: 5vh;
		margin-left: 5vh;
		width: 100%;
		overflow: hidden;
	}

    .back {
		display: flex;
		flex-direction: row;
		align-items: center;
		color: rgba(255, 255, 255, 0.596);
		font-size: 2vh;
		padding: 2vh 1vh;
	}
	.back i {
		margin-right: 1vh;
	}

	.items ul {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		width: 111vh;
	}

	.items ul li {
		width: calc(111vh / 3 - 2vh + 2vh / 3);
		height: 30vh;

		background: var(--product_background);
		border: 1px solid var(--product_border);
        /* 86868665 */
		margin-bottom: 2vh;
	}

	.items ul li:not(:nth-of-type(3n)) {
		margin-right: 2vh;
	}

	.title {
		position: relative;
	}

	.title .borderTop {
		background: var(--product_border);
		width: 100%;
		height: 0.5vh;
	}

	.title p {
		color: var(--product_text);
		font-size: 1.3vh;
		padding: 0.5vh;
		padding-left: 1vh;
		text-transform: uppercase;
	}

	.title .bgTitle {
		border-right: 5vh solid transparent;
		border-top: 2.8vh solid var(--product_border);
		width: 20vh;
		height: 0;
		position: absolute;
		top: 0.5vh;
	}

	.img {
		margin: auto;
		width: 11vh;
		height: 11vh;
		/* background: gray; */
		margin-top: 0.5vh;
	}

	.img input {
		margin-top: 2vh;
		width: 90%;
		height: 90%;

		border-radius: 50%;
	}

	.text {
		/* margin-left: 1vh; */
		display: flex;
		flex-direction: column;
	}

	.text .line {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		padding: 0 1vh;
		margin-bottom: 2vh;
		margin-top: 2vh;
	}

	.text .lineCenter {
		display: flex;
		flex-direction: row;
		justify-content: center;
		margin-top: 3vh;
	}

	.text .columnText {
		font-size: 2vh;
		color: var(--product_text);
	}

	.text .columnSubText {
		font-size: 1vh;
		color: var(--product_text);
        opacity: 0.7;
	}

	.text .lineCenter .columnSubText,
	.text .lineCenter .columnText {
		text-align: center;
	}

	.text .button {
		color: var(--text_themed);
		font-size: 2vh;

		position: relative;
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: center;

		width: 100%;
		height: 4vh;
	}
	.text .button .buttonText {
		position: absolute;
		display: flex;
		flex-direction: row;
		width: max-content;

		justify-content: center;

		background: var(--themed-color);
		border-radius: 5px;
		padding: 0.8vh 3vh;
	}
	.text .button .buttonText i {
		margin-left: 1vh;
	}

    .menu{
        height: 80vh;
        overflow-y: scroll;
    }
</style>