<template>
	<div class="container lg-column">
		<section class="text lg-column lg-expand lg-alignCenter">
			<h2 class="lg-capitalize">{{translate.TR_MANAGE_STORE}}</h2>
			<p>{{translate.TR_ADMIN_TEXT}}</p>
		</section>

		<section class="listOptions lg-line lg-expand lg-justifyCenter">
			<router-link :to="{name: 'seeStores'}">
				<div class="option lg-column lg-alignCenter">
					<div class="img">
						<img src="img/shop-icon.png" alt="">
					</div>
					<p class="optionTitle">{{ToCapitalize(translate.TR_SEE_STORES)}}</p>
					<p class="optionButton">{{translate.TR_SELECT}}</p>
				</div>
			</router-link>
			<router-link :to="{name: 'createStore'}">
				<div class="option lg-column lg-alignCenter">
					<div class="img">
						<img src="img/pen.png" alt="">
					</div>
					<p class="optionTitle">{{ToCapitalize(translate.TR_CREATE_STORE)}}</p>
					<p class="optionButton">{{translate.TR_SELECT}}</p>
				</div>
			</router-link>
			<router-link :to="{name: 'newType', params: {id: -2}}">
				<div class="option lg-column lg-alignCenter">
					<div class="img">
						<img src="img/settings.png" alt="">
					</div>
					<p class="optionTitle">{{ToCapitalize(translate.TR_BASIC_STORE)}}</p>
					<p class="optionButton">{{translate.TR_GENERATE}}</p>
				</div>
			</router-link>
		</section>
	</div>
</template>

<script>
	export default {
        data: function () {
			return {
				translate: this.$store.state.translate,
			};
		},

        methods:{
            ToCapitalize: function(text){
                let copyText = text.toLowerCase();

                let splited = copyText.split(" ");
                for(let i = 0; i < splited.length; i++){
                    splited[i] = splited[i].charAt(0).toUpperCase() + splited[i].slice(1);
                }

                return splited.join(" ");
            }
        }
    };
</script>

<style scoped>
	.container {
		width: 111vh;
		margin: auto;
	}

	.text {
		width: 65%;
		text-align: center;
		margin: auto;
		margin-top: 10vh;
	}

	.text h2 {
		color: rgba(245, 245, 245, 0.856);
        color: rgb(228, 51, 51);
		font-weight: 500;
		font-size: 5vh;
	}

	.text p {
		color: rgba(255, 255, 255, 0.514);
		font-size: 1.5vh;
		margin-top: 5vh;
	}

	.listOptions {
		margin-top: 10vh;
	}

	.option {
		width: 30vh;
		height: 40vh;
		border-radius: 10px;
		background: rgba(15, 15, 15, 0.685);
		margin: 0 2vh;
		transition: 0.5s all;

        border: 1px solid rgba(255, 255, 255, 0.1);
	}

	.option:hover {
		transform: scale(1.1);
	}

	.img {
		width: 15vh;
		height: 15vh;
		margin-top: 2vh;
		/* border-radius: 50%; */
		overflow: hidden;
		/* background: rgba(255, 255, 255, 0.1); */
	}
	.img img {
		width: 100%;
		object-fit: cover;
	}

	.optionTitle {
		font-size: 3vh;
		color: white;
		margin-top: 3vh;
	}

	.optionButton {
		color: white;
		background: rgb(150, 1, 1);
		padding: 1vh 2vh;
		border-radius: 5px;
		margin-top: 6vh;
		font-size: 2vh;
	}
</style>