<template>
	<div :class="{body: use_background}" v-if="openned" 
        :style="{
            '--themed-color': `${colors.themed_color}`,
            '--text_themed': `${colors.text_themed}`,
            '--nav_border': `${colors.nav_border}`,
            '--nav_border_selected': `${colors.nav_border_selected}`,
            '--nav_border_shadow': `${colors.nav_border_shadow}`,
            '--product_background': `${colors.product_background}`,
            '--product_border': `${colors.product_border}`,
            '--product_text': `${colors.product_text}`,
        }"
    >
        <div class="notify-message lg-column" v-if="show_message" >
            <div class="notify-bar"></div>
            <p class="lg-column lg-expand lg-justifyCenter lg-alignCenter">{{message}}</p>
        </div>
		<router-view />
	</div>
</template>

<script>
    import axios from 'axios';
	export default {
		data() {
			return {
				openned: false,
                use_background: true,

                colors: this.$store.state.openned_store.colors,

                show_message: false,
                message: "",
                timeout_message: null,
			};
		},
		methods: {
			SendToClient: function (to, data) {
				axios
					.post(`https://${this.$store.state.name_resource}/${to}`, data)
					.catch((msg) =>
						console.log(`An error has occurred (Main): ${to} : ${msg}`)
					);
			},

			ReceiveLua: function (event) {
				if (!event || !event.data) return;
				const lua = event.data;
				if (lua.config) {
					this.$store.commit("SetNameResource", lua.name_resource);
                    this.$store.commit("SetTranslation", lua.translate);
                    this.$store.commit("SetWeightSystem", lua.weight_system);
                    this.$store.commit("SetUrlImages", lua.url_images);
				} else if (lua.open) {
					this.openned = true;
				} else if(lua.open_store){
                    this.openned = true;

                    // console.log(JSON.stringify(lua.information.colors));
                    // if(!lua.information.colors)
                    //     lua.information.colors = this.$store.state.openned_store.colors;

                    this.$store.commit("SetOpennedStore", lua.information);
                    this.$store.commit("SetCart", []);
                    
                    this.colors = this.$store.state.openned_store.colors;
                    
                    switch (Number(lua.information.template)) {
                        case 1:
                            this.$router.push({name: "itemsTemplate02"});
                            break;
                        default:
                            this.colors.nav_border_shadow = `${this.colors.nav_border_shadow}60`;
                            this.colors.product_background = `${this.colors.product_background}88`;
                            this.colors.product_border = `${this.colors.product_border}65`;

                            this.$router.push({name: "itemsTemplate01"});
                            break;
                    }

                } else if(lua.open_manage){
                    this.openned = true;
                    this.$router.push({name: "admin"});
                }
                else if(lua.disable_background){
                    this.use_background = false;
                }
                else if(lua.enable_background){
                    this.use_background = true;
                }
                else if(lua.show_message){
                    this.show_message = true;
                    this.message = lua.message;

                    if(!this.timeout_message){
                        this.timeout_message = setTimeout(() => {
                            this.show_message = false;
                            this.timeout_message = null;
                        }, 5000);
                    }
                }
                else if(lua.confirm_buy || lua.confirm_sell){
                    setTimeout(() => {
                        this.Close();
                    }, 3000);
                }
                else if(lua.force_close){
                    this.Close();                    
                }
			},
			Close: function () {
				this.SendToClient("Close", {});
				this.openned = false;
			},
			KeyPress: function (event) {
				const key = event.key;
				if (key == "Escape") {
					this.Close();
				}
			},
		},
		created: function () {
			window.addEventListener("message", this.ReceiveLua);
			window.addEventListener("keydown", this.KeyPress);
		},
		unmounted: function () {
			window.removeEventListener("message", this.ReceiveLua);
			window.removeEventListener("keydown", this.KeyPress);
		},
	};
</script>

