<template>
	<div class="container lg-column">
		<div class="title lg-line lg-alignCenter" @click="() => $router.back()">
			<i class="fa-solid fa-caret-left"></i>
			<h1>{{translate.TR_EDITTYPE}}</h1>
		</div>

		<div class="bodyMenu lg-column">
			<div class="menu lg-column lg-expand">
                <p class="menuTitle">{{translate.TR_EDITTYPE_TEXT}}</p>
                <br/>
				<p class="menuTitle">{{translate.TR_SELECT_TYPE}}</p>

				<ul class="lg-column lg-expand">
					<router-link 
                        v-for="(store_type, index) in store_types" :key="`type_${index}`"
                        :to="{name: 'newType', params: {id: store_type.id}}">
						<li class="lg-line lg-justifyBetween lg-alignCenter">
							<p class="lg-uppercase">{{store_type.information.name}}</p>
							<i class="fa-solid fa-chevron-right"></i>
						</li>
					</router-link>
				</ul>
			</div>
		</div>
	</div>
</template>

<script>
    import axios from 'axios';

	export default {
        name: "EditType",

        data: function(){
            return({
                store_types: [],

                translate: this.$store.state.translate,
            });
        },

        methods: {
            SendToClient: function (to, data) {
				axios
					.post(`https://${this.$store.state.name_resource}/${to}`, data)
					.catch((msg) =>
						console.log(
							`An error has occurred (TemplateOneMissions): ${to} : ${msg}`
						)
					);
			},

            ReceiveLua: function (event) {
				if (!event || !event.data) return;
				const lua = event.data;
				if (lua.load_types) {
                    this.store_types = lua.store_types;
				} 
			},
        },

        created: function () {
			window.addEventListener("message", this.ReceiveLua);

            this.SendToClient("LoadTypes", {});
		},
		unmounted: function () {
			window.removeEventListener("message", this.ReceiveLua);
		},
    };
</script>

<style scoped>
	.container {
		width: 111vh;
		margin: auto;
	}

	.title {
		color: rgb(228, 51, 51);
		margin-top: 5vh;
		font-size: 1.8vh;
	}

	.title i {
		font-size: 3vh;
		margin-right: 1vh;
	}

	.bodyMenu {
		margin-top: 5vh;
	}

	.menu:not(:last-child) {
		margin-right: 1vh;
	}

	.menuTitle {
		color: rgba(255, 255, 255, 0.4);
		padding: 0.5vh 0;
	}

	.menu li {
		background: rgba(255, 255, 255, 0.1);
		padding: 1vh;
		color: white;
		border: 1px solid rgba(128, 128, 128, 0.2);
		margin-bottom: 0.5vh;
		/* transition: 0.3s all; */
	}
	.menu li:hover {
		background: rgba(255, 255, 255, 0.2);
		border: 1px solid rgba(128, 128, 128, 1);
	}

	.menu li.selected {
		background: rgba(241, 0, 0, 0.5);
	}

	.menuInfo div:first-of-type {
		margin-top: 1vh;
	}

	.menuInfo p {
		color: rgba(255, 255, 255, 0.3);
	}

	.menuInfo > p {
		color: white;
	}

	.menuFinal {
		padding-left: 2vh;
	}

	.menuFinal h2 {
		color: rgb(228, 51, 51);
	}

	.menuFinal h3 {
		color: rgb(255, 255, 255);
		font-weight: 500;
		text-transform: uppercase;
	}

	.menuFinal > div {
		margin: 2vh 0;
	}

	.menuFinal div:first-of-type {
		/* margin-top: 8vh; */
	}

	.menuFinal p {
		font-size: 2vh;
		color: rgba(255, 255, 255, 0.795);
	}

	.menuFinal .button {
		background: rgba(255, 255, 255, 0.1);
		padding: 1vh;
		color: white;
		border: 1px solid rgba(128, 128, 128, 0.2);
		margin-bottom: 0.5vh;
	}

	.menuFinal .button:hover {
		background: rgba(255, 255, 255, 0.2);
		border: 1px solid rgba(128, 128, 128, 1);
	}
</style>