<template>
	<div class="container">
		<div v-if="is_loading" class="loading lg-column lg-expand lg-justifyCenter lg-alignCenter">
			<Loading />
		</div>
		<div v-else-if="confirmation" class="confirmation">
			<svg class="checkmark" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 52 52">
				<circle class="checkmark__circle" cx="26" cy="26" r="25" fill="none" />
				<path class="checkmark__check" fill="none" d="M14.1 27.2l7.1 7.2 16.7-16.8" />
			</svg>
		</div>

		<section class="items" v-else>
			<div class="cartTitle">
				<p>{{translate.TR_PAYMENT}}</p>
			</div>

			<div class="cartBody">
				<div class="cartItems">
					<p class="cartItemsTitle">{{translate.TR_SUPERMARKET_CART}}</p>

					<div class="itemScope lg-hideScroll">
						<ul class="menu lg-hideScroll">
							<li v-for="(product, index) in cart" :key="`cart_${index}`">
								<p class="itemWeight" v-if="weight_system">{{product.weight}} {{translate.TR_SIMBOL_WEIGHT}}</p>

								<div class="img">
									<img :src="`${url_images}${product.name}.png`" alt="">
								</div>
								<div class="title">
									<p>{{product.label}}</p>
								</div>

								<div class="price">
									<p class="price">{{translate.TR_SIMBOL_MONEY}} {{product.price}}</p>
								</div>

								<div class="lg-line lg-alignCenter lg-justifyCenter">
									<p class="itemCartButton" @click="ChangeAmount(index, -1)">-</p>
									<div class="cartAmount">
										<input type="number" v-model="product.selected_amount" @input="ValidateInput(index)" />
										<p>/</p>
										<p>{{product.amount}}</p>
									</div>
									<p class="itemCartButton" @click="ChangeAmount(index, 1)">+</p>
								</div>
							</li>
						</ul>
					</div>

					<div id="cartBodyFooter">
						<div id="cartBodyFooterButton" @click="ClickBack">
							<i class="fas fa-arrow-left"></i>
							<p>{{translate.TR_CONTINUE_SHOPPING}}</p>
						</div>

						<p id="cartBodyFooterTotal">{{translate.TR_TOTAL}} {{translate.TR_SIMBOL_MONEY}}{{GetTotalPrice()}}</p>
					</div>
				</div>

				<div id="cartPayment">
					<p class="cartPaymentTitle">{{translate.TR_PAYMENT_OPTIONS}}</p>

					<ul id="cartPaymentList">
						<li class="cartPaymentItem" v-for="(item, index) in payment_methods" :key="`payment_${index}`" :class="{cartPaymentItemChecked : buy_method == index}" @click="SetBuyMethod(index)">
							<div class="cartPaymentName">
								<img :src="item.img" alt="" class="cartPaymentImage">
								<p class="cartPaymentName">{{item.name}}</p>
							</div>

							<div class="checkedPayment"></div>
						</li>
					</ul>

					<div id="cartPaymentButtonBuy" :class="{disabledButton : cart.length <= 0}" @click="FinishBuy">{{translate.TR_BUY_NOW}}</div>
				</div>
			</div>
		</section>
	</div>
</template>

<script>
	import Loading from "@/components/Loading2.vue";
	import axios from "axios";
	export default {
		name: "TemplateTwoCart",
		components: {
			Loading,
		},
		data: function () {
			return {
				id: this.$store.state.openned_store.id,
				can_walletmoney: this.$store.state.openned_store.can_walletmoney,
				can_bankmoney: this.$store.state.openned_store.can_bankmoney,
				can_blackmoney: this.$store.state.openned_store.can_blackmoney,
				can_item: this.$store.state.openned_store.can_item ? this.$store.state.openned_store.can_item : "",
				can_itemlabel: this.$store.state.openned_store.can_itemlabel,
				cart: [],

				confirmation: false,

				is_loading: false,

				payment_methods: [
					{ name: this.$store.state.translate.TR_BANK, img: "img/card.png" },
					{ name: this.$store.state.translate.TR_WALLET, img: "img/wallet.png" },
				],

				buy_method: 0,

				weight_system: this.$store.state.weight_system,
				url_images: this.$store.state.url_images,
				translate: this.$store.state.translate,
			};
		},

		methods: {
			ValidateInput: function (product_index) {
				if (this.cart[product_index].selected_amount < 0) {
					this.cart[product_index].selected_amount = 1;
				}
				else if (this.cart[product_index].selected_amount > this.cart[product_index].amount) {
					this.cart[product_index].selected_amount = this.cart[product_index].amount;
				}
				else if (this.cart[product_index].selected_amount == 0) {
					this.cart.splice(product_index, 1);
				}
			},
			ChangeAmount: function (product_index, amount) {
				if (
					this.cart[product_index].selected_amount >=
					this.cart[product_index].amount &&
					amount > 0
				)
					return;

				this.cart[product_index].selected_amount += amount;

				if (this.cart[product_index].selected_amount <= 0) {
					this.cart.splice(product_index, 1);
				}
			},
			GetTotalPrice: function () {
				let total = 0;
				this.cart.forEach(
					(product) => (total += product.price * product.selected_amount)
				);

				return total;
			},

			SetBuyMethod: function (value) {
				this.buy_method = value;
			},

			BuyWallet: function () {
				if (this.is_loading || this.cart.length == 0) return;

				this.SendToClient("BuyWallet", {
					id: this.id,
					cart: this.cart,
				});

				this.is_loading = true;
			},
			BuyBank: function () {
				if (this.is_loading || this.cart.length == 0) return;

				this.SendToClient("BuyBank", {
					id: this.id,
					cart: this.cart,
				});

				this.is_loading = true;
			},
			BuyBlackMoney: function () {
				if (this.is_loading || this.cart.length == 0) return;

				this.SendToClient("BuyBlackMoney", {
					id: this.id,
					cart: this.cart,
				});

				this.is_loading = true;
			},

			BuyItemMoney: function () {
				if (this.is_loading || this.cart.length == 0) return;

				this.SendToClient("BuyItemMoney", {
					id: this.id,
					cart: this.cart,
				});

				this.is_loading = true;
			},

			FinishBuy: function () {
				switch (this.buy_method) {
					case 1:
						this.BuyWallet();
						break;
					default:
						if (this.can_blackmoney)
							this.BuyBlackMoney();
						else if (!this.can_blackmoney && this.can_item != '')
							this.BuyItemMoney();
						else
							this.BuyBank();
						break;
				}
			},

			ClickBack: function () {
				this.$router.back();
			},

			SendToClient: function (to, data) {
				axios
					.post(`https://${this.$store.state.name_resource}/${to}`, data)
					.catch((msg) =>
						console.log(`An error has occurred (TemplateTwoCart): ${to} : ${msg}`)
					);
			},

			ReceiveLua: function (event) {
				if (!event || !event.data) return;
				const lua = event.data;
				if (lua.confirm_cart) {
					this.$store.commit("SetCart", []);
					this.$store.commit("UpdateProducts", lua.products);
					this.cart = this.$store.state.cart;
					this.is_loading = false;
					this.confirmation = true;

					setTimeout(() => (this.confirmation = false), 3000);
				} else if (lua.cancel_cart) {
					this.$store.commit("UpdateProducts", lua.products);
					this.is_loading = false;
				}
			},
		},

		mounted: function () {
			this.cart = this.$store.state.cart;

			if (this.can_blackmoney)
				this.payment_methods = [{ name: this.translate.TR_BLACKMONEY, img: "img/blackMoney.png" },];
			else if (!this.can_blackmoney && this.can_item != '')
				this.payment_methods = [{ name: this.can_itemlabel, img: "img/marca.png" },];
		},

		created: function () {
			window.addEventListener("message", this.ReceiveLua);
		},
		unmounted: function () {
			window.removeEventListener("message", this.ReceiveLua);
		},
	};
</script>

