<template>
	<div class="card">
		<div class="header">
			<div class="div_image_v" :class="{loading: loading}">
				<div class="image">
					<svg v-if="!loading" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
						<g id="SVGRepo_bgCarrier" stroke-width="0"></g>
						<g id="SVGRepo_tracerCarrier" stroke-linecap="round" stroke-linejoin="round"></g>
						<g id="SVGRepo_iconCarrier">
							<path d="M20 7L9.00004 18L3.99994 13" stroke="#000000" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"></path>
						</g>
					</svg>

                    <Loading v-else />
				</div>
			</div>
			<div class="content">
				<span class="title" :class="{loading: loading}">{{ title }}</span>
				<p class="message">{{ text }}</p>
			</div>
		</div>
	</div>
</template>

<script>
	import Loading from "@/components/Loading2.vue";
	export default {
		name: "PopUp",
		components: { Loading },
		props: {
			title: { default: "" },
			text: { default: "" },
            loading: { default: true },
		},

		data: function () {
			return {
				amountValue: 1,
				translate: this.$store.state.translate,
			};
		},

		methods: {
			UpdateAmount: function (value) {
				this.amountValue += value;

				this.ValidateUpdateAmount();

				if (this.OnInput) {
					this.OnInput(this.amountValue);
				}
			},

			ValidateUpdateAmount: function () {
				if (Number.isNaN(this.amountValue) || this.amountValue <= 0)
					this.amountValue = 1;
				else if (this.amountValue > this.maxAmount)
					this.amountValue = this.maxAmount;

				if (this.OnInput) {
					this.OnInput(this.amountValue);
				}
			}
		},

		mounted: function () {
			this.amountValue = this.amount;
		},
	};
</script>

<style scoped>
	.card {
		overflow: hidden;
		position: relative;
		text-align: left;
		border-radius: 0.5rem;
		max-width: 590px;
		box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1),
			0 10px 10px -5px rgba(0, 0, 0, 0.04);
		background-color: #fff;

        min-width: 600px;

        animation: startAnim 1s;
	}

    @keyframes startAnim {
		0% {
			top: 100%;
			/* transform: translate(-50%, -100%); */
		}
		100% {
			top: 50%;
			transform: translate(-50%, -50%);
		}
	}

	.div_image_v {
		background: #47c9a2;
		border-bottom: none;
		position: relative;
		text-align: center;
		margin: -20px -20px 0;
		border-radius: 5px 5px 0 0;
		padding: 35px;
	}

	.dismiss {
		position: absolute;
		right: 10px;
		top: 10px;
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 0.5rem 1rem;
		background-color: #fff;
		color: black;
		border: 2px solid #d1d5db;
		font-size: 1rem;
		font-weight: 300;
		width: 30px;
		height: 30px;
		border-radius: 7px;
		transition: 0.3s ease;
	}

	.dismiss:hover {
		background-color: #ee0d0d;
		border: 2px solid #ee0d0d;
		color: #fff;
	}

	.header {
		padding: 1.25rem 1rem 1rem 1rem;
	}

	.image {
		display: flex;
		margin-left: auto;
		margin-right: auto;
		background-color: #e2feee;
		flex-shrink: 0;
		justify-content: center;
		align-items: center;
		width: 3rem;
		height: 3rem;
		border-radius: 9999px;
		animation: animate 0.6s linear alternate-reverse infinite;
		transition: 0.6s ease;
	}

	.image svg {
		color: #0afa2a;
		width: 2rem;
		height: 2rem;
	}

	.content {
		margin-top: 0.75rem;
		text-align: center;
	}

	.title {
		color: #066e29;
		font-size: 1rem;
		font-weight: 600;
		line-height: 1.5rem;
	}

	.message {
		margin-top: 0.5rem;
		color: #595b5f;
		font-size: 0.875rem;
		line-height: 1.25rem;
	}

	@keyframes animate {
		from {
			transform: scale(1);
		}

		to {
			transform: scale(1.09);
		}
	}

    .div_image_v.loading {
		background: #c9b547;
    }

    .title.loading {
		color: #6e5b06;
    }

</style>