<template>
	<div class="loader"
        :style="{
            border: `2vh solid ${color_secondary}`,
            borderTop: `2vh solid ${color_primary}`,
            width: size,
            height: size,
        }"
    ></div>
</template>

<script>
	export default {
        name: "SimpleLoader",
        props: {
            color_primary: {
                default: "#3498db",
            },
            color_secondary: {
                default: "#f3f3f3",
            },
            size: {
                default: "13vh",
            }
        }
    };
</script>

<style scoped >
	.loader {
		border: 2vh solid #f3f3f3; /* Light grey */
		border-top: 2vh solid #3498db; /* Blue */
		border-radius: 50%;
		width: 13vh;
		height: 13vh;
		animation: spin 2s linear infinite;
	}

	@keyframes spin {
		0% {
			transform: rotate(0deg);
		}
		100% {
			transform: rotate(360deg);
		}
	}
</style>