		methods: {
			ChangeStage: function (amount) {
				this.stage += amount;
				this.search_item_server = "";
				this.search_products = "";

				if (this.stage == 1 && Number(this.id) > -1) this.stage += amount;

				if (this.stage > 13) {
					this.SendToClient("SaveType", {
						id: Number(this.id),
						information: this.information,
						operation: Number(this.id) < 0 ? "creating" : "saving",
						is_basic: this.is_basic,
					});

					this.$router.back();
				}
			},

			ChangeTemplate: function (value) {
				this.information.template = value;
				this.information.colors = {
					...this.$store.state.templates[value].colors,
				};
				this.ChangeStage(1);
			},

			AddProduct: function (product) {
				if (
					this.information.products.filter((item) => item.name == product.name)
						.length == 0
				) {
					this.information.products.push({
						name: product.name,
						label: product.label,
						amount: 0,
						weight: product.weight,
						price: 0,
						max_price: 0,
					});
					this.information.products_mission[product.name] = {
						name: "",
						label: "",
						price_mission: 0.0,
						max_request: 0,
						payment: 0,
					};
				}
			},

			RemoveProduct: function (index) {
				this.information.products.splice(index, 1);
			},

			SelectProduct: function (item) {
				this.selected_product = item;
			},

			SelectMissionProduct: function (item) {
				this.information.products_mission[this.selected_product.name].name =
					item.name;
				this.information.products_mission[this.selected_product.name].label =
					item.label;
			},

			IsMissionItem: function (to, from) {
				return this.information.products_mission[to].name == from.name;
			},

			ValidateInputs: function () {
				for (const key in this.information.products_mission) {
					const object = this.information.products_mission[key];
					if (Number.isNaN(object.price_mission) || object.price_mission <= 0) {
						this.information.products_mission[key].price_mission = 0.0;
					}
					if (Number.isNaN(object.max_request) || object.max_request <= 0) {
						this.information.products_mission[key].max_request = 0;
					}
					if (Number.isNaN(object.payment) || object.payment <= 0) {
						this.information.products_mission[key].payment = 0;
					}

					this.information.products_mission[key].max_request = Math.floor(
						this.information.products_mission[key].max_request
					);
				}

				this.information.products.forEach((object) => {
					if (Number.isNaN(object.price) || object.price <= 0) {
						object.price = 0.0;
					}
					if (Number.isNaN(object.amount) || object.amount <= 0) {
						object.amount = 0;
					}
					if (Number.isNaN(object.max_price) || object.max_price <= 0) {
						object.max_price = 0;
					}

					object.amount = Math.floor(object.amount);
				});
			},

			FilterItems: function () {
				return this.items_server.filter((el) => {
					if (
						el.label.toLowerCase().includes(this.search_item_server.toLowerCase())
					) {
						return (
							this.information.products.filter((prod) => prod.name == el.name)
								.length == 0
						);
					}

					return false;
				});
			},

			SetCan: function (type, value) {
				if (type == 1) this.information.can_moneybox_stolen = value;
				else if (type == 2) this.information.can_coffre_stolen = value;
				else if (type == 3) this.information.can_npc_stolen = value;
				else if (type == 4) this.information.can_have_workerss = value;
				else if (type == 5) this.information.can_walletmoney = value;
				else if (type == 6) this.information.can_bankmoney = value;
				else if (type == 7) this.information.can_blackmoney = value;
				else if (type == 8) this.information.can_job = value;
				else if (type == 9) this.information.can_blip = value;
				else if (type == 10) this.information.can_marker = value;

				this.ChangeStage(1);
			},

			SetUseItem: function (value) {
				this.use_item = value;

				if (!value) {
					this.information.can_item = "";
					this.information.can_itemlabel = "";
				}
			},

			ChangeBlip: function (value) {
				this.information.blip = value;
				this.SetCan(9, true);
			},

			ChangeBlipColor: function (value) {
				this.information.blip_color = value;
				this.ChangeStage(1);
			},

			ChangeMarker: function (value) {
				this.information.marker = value;
				this.SetCan(10, true);
			},

			Initialize: function () {
				// this.translate = this.$store.state.translate;
				this.SendToClient("GetAllItems", {});

				if (this.id > -1) {
					this.SendToClient("LoadInformation", {
						id: Number(this.id),
					});

					this.is_loading = true;
				}

				this.is_basic = this.id == -2;
			},

			SendToClient: function (to, data) {
				axios
					.post(`https://${this.$store.state.name_resource}/${to}`, data)
					.catch((msg) =>
						console.log(`An error has occurred (NewType): ${to} : ${msg}`)
					);
			},

			ReceiveLua: function (event) {
				if (!event || !event.data) return;
				const lua = event.data;
				if (lua.receive_all_items) {
					this.items_server = lua.items;
				} else if (lua.load_information) {
					this.information = lua.information;
					this.is_loading = false;
				}
			},
		},

		created: function () {
			window.addEventListener("message", this.ReceiveLua);
			this.Initialize();
		},
		unmounted: function () {
			window.removeEventListener("message", this.ReceiveLua);
		},
	};
</script>

