<template>
	<div class="container">
		<section class="items">
            <div class="cartTitle">
				<p>{{translate.TR_STORE_BALANCE}}</p>
			</div>

			<div class="box lg-column">
                <div v-if="is_loading" class="loading lg-column lg-expand lg-justifyCenter lg-alignCenter" >
                    <Loading />
                </div>

                <div v-else >
                    <div class="img">
                        <img src="img/salary.png" alt="">
                    </div>
                    <div class="text">
                        <div class="lineCenter">
                            <div>
                                <p class="columnText">{{translate.TR_SIMBOL_MONEY}} {{money.toFixed(2)}}</p>
                                <p class="columnSubText">{{translate.TR_MONEY_AVAILABLE}}</p>
                            </div>
                        </div>
                        <div class="line">
                            <div class="inputValue">
                                <input type="number" :placeholder="translate.TR_TYPE_VALUE" min="1" step="1" v-model="deposit" @input="ValidateInputs" >
                                <div class="button">
                                    <div class="buttonText deposit" @click="DepositMoney">
                                        <p>{{translate.TR_DEPOSIT}}</p>
                                        <!-- <i class="fa-solid fa-check"></i> -->
                                    </div>
                                </div>
                            </div>
                            <div class="inputValue">
                                <input type="number" :placeholder="translate.TR_TYPE_VALUE" min="1" step="1" v-model="withdraw" @input="ValidateInputs">
                                <div class="button">
                                    <div class="buttonText" @click="WithdrawMoney">
                                        <p>{{translate.TR_WITHDRAW}}</p>
                                        <!-- <i class="fa-solid fa-check"></i> -->
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
			</div>
		</section>
	</div>
</template>

<script>
	import axios from "axios";
    import Loading from "@/components/Loading2.vue";

	export default {
		name: "TemplateOneItems",

        components: {
            Loading
        },

		data: function () {
			return {
                id: this.$store.state.openned_store.id,
				money: 5,
				deposit: 0,
				withdraw: 0,

				is_loading: false,

				translate: this.$store.state.translate,
			};
		},

		methods: {
            DepositMoney: function(){
                this.SendToClient("DepositMoney", {
                    id: this.id,
                    value: this.deposit,
                });
                this.is_loading = true;
            },
            WithdrawMoney: function(){
                this.SendToClient("WithdrawMoney", {
                    id: this.id,
                    value: this.withdraw,
                });
                this.is_loading = true;
            },
			ValidateInputs: function () {
				if (Number.isNaN(this.deposit) || this.deposit < 0) {
					this.deposit = 1;
				}
				if (Number.isNaN(this.withdraw) || this.withdraw < 0) {
					this.withdraw = 1;
				}

				this.deposit = Math.floor(this.deposit);
				this.withdraw = Math.floor(this.withdraw);
			},
			SendToClient: function (to, data) {
				axios
					.post(`https://${this.$store.state.name_resource}/${to}`, data)
					.catch((msg) =>
						console.log(`An error has occurred (TemplateTwoBalance): ${to} : ${msg}`)
					);
			},

			ReceiveLua: function (event) {
				if (!event || !event.data) return;
				const lua = event.data;
				if (lua.receive_money) {
					this.money = lua.money;
                    this.is_loading = false;
				}
			},
		},

		created: function () {
			window.addEventListener("message", this.ReceiveLua);

			// this.is_loading = true;
			this.SendToClient("GetStoreMoney", {
				id: this.$store.state.openned_store.id,
			});
		},
		unmounted: function () {
			window.removeEventListener("message", this.ReceiveLua);
		},
	};
</script>

<style scoped>
	.container {
		background-color: var(--nav_border_selected);
		margin-top: -0.1vh;
		flex: 1;
		padding: 2vh 5vh;
	}

    .cartTitle {
		text-align: center;
		font-size: 5vh;
		font-weight: bolder;
		color: var(--nav_border);
	}

	.items {
		/* margin-top: 5vh; */
		margin-left: 5vh;
		width: 100%;
		overflow: hidden;
	}

	.items .box {
		width: 111vh;
		height: 70vh;
        margin: auto;
	}
	.img {
		margin: auto;
		width: 15vh;
		height: 15vh;
		/* background: gray; */
		margin-top: 5vh;

        text-align: center;
	}

	.img img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	.text {
		/* margin-left: 1vh; */
		display: flex;
		flex-direction: column;
	}

	.text .line {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		padding: 0 1vh;
		margin-bottom: 2vh;
		margin-top: 2vh;
	}

	.text .lineCenter {
		display: flex;
		flex-direction: row;
		justify-content: center;
		margin-top: 3vh;
	}

	.text .columnText {
		font-size: 4vh;
        font-weight: 500;
        color: var(--themed-color);
        text-shadow: 1px 1px 1px rgb(158, 158, 158);
        /* margin-top: 5vh; */
	}

	.text .columnSubText {
		font-size: 2vh;
		color: var(--themed-color);
        text-shadow: 1px 1px 1px rgb(158, 158, 158);
	}

	.text .lineCenter .columnSubText,
	.text .lineCenter .columnText {
		text-align: center;
	}

	.text .button {
		color: var(--text_themed);
		font-size: 2vh;

		position: relative;
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: center;

		width: 100%;
		height: 4vh;
	}
	.text .button .buttonText {
		position: absolute;
		display: flex;
		flex-direction: row;
		width: max-content;

		justify-content: center;

		background-color: rgb(104, 104, 104);
		border-radius: 5px;
		padding: 0.8vh 3vh;
	}
    .text .button .buttonText.deposit{
        background-color: var(--themed-color);
    }
    
	.text .button .buttonText i {
		margin-left: 1vh;
	}

	.inputValue input {
		background: rgb(255, 255, 255);
		border: 1px solid rgba(68, 68, 68, 0.1);
		outline: none;
		font-size: 2vh;
		padding: 1vh;
		margin: auto;
		text-align: center;
		margin-bottom: 2vh;
		width: 90%;
		color: rgb(0, 0, 0);
		border-radius: 5px;
	}

	.inputValue {
		flex: 1;
		margin-top: 5vh;
		display: flex;
		flex-direction: column;
		align-items: center;
	}

    .loading{
        margin-bottom: 10vh;
    }
</style>