<template>
	<div class="container">
		<PopUp v-if="pop_up.openned" :text="pop_up.text" :subtexts="pop_up.subtexts" :image="pop_up.image" :img_url="pop_up.img_url" :buttons="pop_up.buttons" :field="pop_up.field" :confirmation="pop_up.confirmation" :loading="pop_up.loading" :error="pop_up.error" :quest="pop_up.quest" :func_confirm="pop_up.func_confirm" :func_cancel="pop_up.func_cancel" :func_input="pop_up.func_input" />
		<section class="items column">
			<div class="box lg-column ">
				<div class="title">
					<div class="borderTop"></div>
					<div class="bgTitle"></div>
					<p>{{translate.TR_STOCK}}</p>
				</div>
				<div class="back" @click="() => $router.back()">
					<i class="fa-solid fa-caret-left"></i>
					<p>{{translate.TR_BACK}}</p>
				</div>

				<div v-if="is_loading" class="loading lg-column lg-expand lg-justifyCenter lg-alignCenter">
					<Loading color_primary="rgba(145, 14, 14, 1)" color_secondary="rgba(255, 255, 255, 0.8)" />
				</div>

				<div class="lg-line lg-expand" v-else>
					<div class="stock lg-column lg-expand">
						<h2 class="titleList">{{translate.TR_STOCK_STORE}}</h2>
						<ul class="listItems lg-hideScroll">
							<li class="lg-line lg-alignCenter" v-for="(product, index) in stock" :key="`prod_${index}`">
								<div class="img" :class="{grayScale: product.amount < 1}">
									<img :src="`${url_images}${product.name}.png`" alt="">
								</div>
								<div class="itemText lg-line lg-text-white lg-alignCenter lg-justifyBetween lg-expand">
									<p class="productName">{{product.label}}</p>
									<p class="productAmount">{{product.amount}} {{translate.TR_UNITY}}</p>
									<p class="buttonHover lg-button-transparent" @click="PopUpPutForSale(index)">{{translate.TR_PUT_SALE}}</p>
									<!-- <p class="buttonHover lg-button-transparent" @click="PopUpMission(index)">{{translate.TR_CREATE_MISSION}}</p> -->
								</div>
							</li>

						</ul>
					</div>
					<div class="lg-column">

					</div>
					<div class="products lg-column lg-expand">
						<h2 class="titleList">{{translate.TR_PRODUCTS_STORE}}</h2>
						<ul class="listItems lg-hideScroll">
							<li class="lg-line lg-alignCenter" v-for="(product, index) in products" :key="`stock_${index}`">
								<div class="img" :class="{grayScale: product.amount < 1}">
									<img :src="`${url_images}${product.name}.png`" alt="">
								</div>
								<div class="itemText lg-line lg-text-white lg-alignCenter lg-justifyBetween lg-expand">
									<p class="productName">{{product.label}}</p>
									<p class="productAmount">{{product.amount}} {{translate.TR_UNITY}}</p>
									<p class="buttonHover lg-button-transparent" @click="PopUpRemove(index)">{{translate.TR_REMOVE}}</p>
									<p class="buttonHover lg-button-transparent" @click="PopUpPrice(index)">{{translate.TR_CHANGE_PRICE}}</p>
								</div>
							</li>
						</ul>
					</div>
				</div>
			</div>
		</section>
	</div>
</template>

<script>
	import axios from "axios";
	import Loading from "@/components/Loading.vue";
	import PopUp from "@/components/PopUp.vue";
	export default {
		name: "TemplateOneItems",

		components: {
			Loading,
			PopUp,
		},

		data: function () {
			return {
				id: this.$store.state.openned_store.id,
				stock: [],
				products: [],
				products_mission: [],

				is_loading: false,

				pop_up: {
					openned: false,
					text: "",
					buttons: true,
					field: false,
					confirmation: false,
					loading: false,
					error: false,
					quest: true,
					image: false,
					img_url: "",
					func_confirm: false,
					func_cancel: this.PopUpCancel,
					func_input: false,
				},

                url_images: this.$store.state.url_images,
				translate: this.$store.state.translate,
			};
		},

		methods: {
			Confirm: function () {
				this.pop_up.confirmation = true;
				this.pop_up.quest = false;
			},

			PopUpMission: function (index) {
				this.pop_up.text = `${this.translate.TR_REQUEST_MISSION_AMOUNT} [${this.stock[index].label}]`;
				this.pop_up.subtexts = [
					`${this.translate.TR_REQUIRED_ITEM}: ${
						this.products_mission[this.stock[index].name].label
					}`,
					`${this.translate.TR_MAX_REQUEST} ${
						this.products_mission[this.stock[index].name].max_request
					}`,
					`${this.translate.TR_REQUEST_PRICE} ${
						this.products_mission[this.stock[index].name].price_mission
					}`,
				];
				this.pop_up.openned = true;
				this.pop_up.field = true;
				this.pop_up.buttons = true;
				this.pop_up.image = true;
				this.pop_up.img_url = `${this.url_images}${this.stock[index].name}.png`;

				this.pop_up.func_input = (amount, cb) => {
					amount = Number(amount);

					if (!Number.isNaN(amount) && amount > 0) {
						amount = Math.floor(amount);

						if (
							amount > this.products_mission[this.stock[index].name].max_request
						) {
							this.pop_up.subtexts[2] = `${this.translate.TR_REQUEST_PRICE}: ${
								this.products_mission[this.stock[index].name].price_mission *
								this.products_mission[this.stock[index].name].max_request
							}`;
							cb(this.products_mission[this.stock[index].name].max_request);
						} else {
							this.pop_up.subtexts[2] = `${this.translate.TR_REQUEST_PRICE}: ${
								this.products_mission[this.stock[index].name].price_mission *
								amount
							}`;
						}
					}
				};

				this.pop_up.func_confirm = (amount) => {
					amount = Number(amount);

					if (!Number.isNaN(amount) && amount > 0) {
						amount = Math.floor(amount);

						this.SendToClient("CreateMission", {
							id: this.id,
							product: this.stock[index],
							amount: amount,
						});

						this.PopUpWait();
					}
				};
			},

			PopUpPutForSale: function (index) {
				this.pop_up.text = `${this.translate.TR_PUT_SALE_AMOUNT} [${this.stock[index].label}]`;
				this.pop_up.openned = true;
				this.pop_up.field = true;
				this.pop_up.buttons = true;
				this.pop_up.image = true;
				this.pop_up.img_url = `${this.url_images}${this.stock[index].name}.png`;

				this.pop_up.func_input = (amount, cb) => {
					amount = Number(amount);

					if (!Number.isNaN(amount) && amount > 0) {
						amount = Math.floor(amount);

						if (amount > this.stock[index].amount) {
							cb(this.stock[index].amount);
						}
					}
				};

				this.pop_up.func_confirm = (amount) => {
					amount = Number(amount);

					if (!Number.isNaN(amount) && amount > 0) {
						amount = Math.floor(amount);

						this.SendToClient("PutForSaleProduct", {
							id: this.id,
							product: this.stock[index],
							amount: amount,
						});

						this.PopUpWait();
					}
				};
			},

			PopUpRemove: function (index) {
				this.pop_up.text = `${this.translate.TR_REMOVE_AMOUNT} [${this.products[index].label}]`;
				this.pop_up.openned = true;
				this.pop_up.field = true;
				this.pop_up.buttons = true;
				this.pop_up.image = true;
				this.pop_up.img_url = `${this.url_images}${this.products[index].name}.png`;

				this.pop_up.func_input = (amount, cb) => {
					amount = Number(amount);

					if (!Number.isNaN(amount) && amount > 0) {
						amount = Math.floor(amount);

						if (amount > this.products[index].amount) {
							cb(this.products[index].amount);
						}
					}
				};

				this.pop_up.func_confirm = (amount) => {
					amount = Number(amount);

					if (!Number.isNaN(amount) && amount > 0) {
						amount = Math.floor(amount);

						this.SendToClient("RemoveProduct", {
							id: this.id,
							product: this.products[index],
							amount: amount,
						});

						this.PopUpWait();
					}
				};
			},

			PopUpPrice: function (index) {
				this.pop_up.text = `${this.translate.TR_NEW_PRICE} [${this.products[index].label}]`;
				this.pop_up.subtexts = [
					`${this.translate.TR_CURRENT}: ${this.products[index].price}`,
					`${this.translate.TR_MAX_PRICE} ${this.products[index].max_price}`,
				];
				this.pop_up.openned = true;
				this.pop_up.field = true;
				this.pop_up.buttons = true;
				this.pop_up.image = true;
				this.pop_up.img_url = `${this.url_images}${this.products[index].name}.png`;

				this.pop_up.func_input = (new_price, cb) => {
					new_price = Number(new_price);

					if (!Number.isNaN(new_price) && new_price > 0) {
						new_price = Math.floor(new_price);

						if (new_price > this.products[index].max_price) {
							cb(this.products[index].max_price);
						}
					}
				};

				this.pop_up.func_confirm = (new_price) => {
					new_price = Number(new_price);

					if (!Number.isNaN(new_price) && new_price > 0) {
						new_price = Math.floor(new_price);

						this.SendToClient("ChangePriceProduct", {
							id: this.id,
							product: this.products[index],
							new_price: new_price,
						});

						this.PopUpWait();
					}
				};
			},

			PopUpWait: function () {
				this.PopUpCancel();

                this.pop_up.openned = true;
				this.pop_up.text = this.translate.TR_WAIT;
				this.pop_up.loading = true;
			},

			PopUpCancel: function () {
				this.pop_up.openned = false;
				this.pop_up.text = "";
				this.pop_up.subtexts = [];
				this.pop_up.buttons = false;
				this.pop_up.field = false;
				this.pop_up.confirmation = false;
				this.pop_up.loading = false;
				this.pop_up.error = false;
				this.pop_up.quest = false;
				this.pop_up.image = false;
				this.pop_up.img_url = "";
				this.pop_up.func_confirm = this.PopUpCancel;
				this.pop_up.func_input = false;
			},

			LoadStock: function () {
				this.is_loading = true;
				this.SendToClient("GetStoreStock", {
					id: this.$store.state.openned_store.id,
				});
			},

			SendToClient: function (to, data) {
				axios
					.post(`https://${this.$store.state.name_resource}/${to}`, data)
					.catch((msg) =>
						console.log(
							`An error has occurred (TemplateOneStock): ${to} : ${msg}`
						)
					);
			},

			ReceiveLua: function (event) {
				if (!event || !event.data) return;
				const lua = event.data;
				if (lua.receive_stock) {
					this.products = lua.products;
					this.stock = lua.stock;
					this.products_mission = lua.products_mission;
					this.is_loading = false;
					this.PopUpCancel();

					this.$store.commit("UpdateProducts", this.products);
					this.$store.commit("SetCart", []);
				} else if (lua.created_mission) {
					this.pop_up.loading = false;
					this.pop_up.confirmation = true;
					this.pop_up.text = "";

                    this.$store.commit("UpdateMissions", lua.missions);

					setTimeout(() => {
						this.PopUpCancel();
					}, 3000);
				}
			},
		},

		created: function () {
			window.addEventListener("message", this.ReceiveLua);

			this.LoadStock();
		},
		unmounted: function () {
			window.removeEventListener("message", this.ReceiveLua);
		},
	};
</script>

<style scoped>
	.container {
		flex: 1;
		width: 150vh;
		margin: auto;
	}

	.items {
		margin-top: 5vh;
		margin-left: 5vh;
		width: 100%;
		overflow: hidden;
	}

	.items .box {
		width: 111vh;
		height: 70vh;

		background: var(--product_background);
		border: 1px solid var(--product_border);

		margin-bottom: 2vh;
	}

	.title {
		position: relative;
	}

	.title .borderTop {
		background: var(--product_border);
		width: 100%;
		height: 0.5vh;
	}

	.title p {
		color: white;
		font-size: 1.3vh;
		padding: 0.5vh;
		padding-left: 1vh;
		text-transform: uppercase;
	}

	.title .bgTitle {
		border-right: 5vh solid transparent;
		border-top: 2.8vh solid var(--product_border);
		width: 20vh;
		height: 0;
		position: absolute;
		top: 0.5vh;
	}

	.back {
		display: flex;
		flex-direction: row;
		align-items: center;
		color: rgba(255, 255, 255, 0.596);
		font-size: 2vh;
		padding: 2vh 1vh;
	}
	.back i {
		margin-right: 1vh;
	}

	.titleList {
		padding: 1vh;
		margin-bottom: 2vh;
		font-weight: 500;
		color: rgba(255, 255, 255, 0.767);

		border-bottom: 5px solid rgba(255, 255, 255, 0.1);
		/* border-radius: 5px; */
	}

	.stock,
	.products {
		overflow: hidden;
		max-height: 60vh;
	}

	.stock {
		margin-right: 5vh;
	}

	.listItems {
		overflow-y: scroll;
	}

	.listItems li {
		background: rgba(255, 255, 255, 0.1);
		padding: 1vh 0;
		padding-right: 2vh;
		border-radius: 3px;
		margin-bottom: 0.5vh;
		font-size: 1.5vh;
	}

	.img {
		width: 5vh;
		height: 5vh;
	}

	.img img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	.itemText {
	}

	.text .button {
		color: white;
		font-size: 2vh;

		position: relative;
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: center;

		width: 100%;
		height: 4vh;
	}
	.text .button .buttonText {
		position: absolute;
		display: flex;
		flex-direction: row;
		width: max-content;

		justify-content: center;

		background: rgba(145, 14, 14, 1);
		border-radius: 5px;
		padding: 0.8vh 3vh;
	}
	.text .button .buttonText i {
		margin-left: 1vh;
	}

	.buttonHover:hover {
		background: var(--themed-color);
		color: var(--text_themed);
		transition: 0.3 all;
	}

	.inputValue input {
		background: rgba(255, 255, 255, 0.05);
		border: none;
		outline: none;
		font-size: 2vh;
		padding: 1vh;
		margin: auto;
		text-align: center;
		margin-bottom: 2vh;
		width: 90%;
		color: white;
		border-radius: 5px;
	}

	.inputValue {
		flex: 1;
		margin-top: 5vh;
		display: flex;
		flex-direction: column;
		align-items: center;
	}
	.loading {
		margin-bottom: 10vh;
	}

	.productName,
	.productAmount {
		width: 7vh;
		text-overflow: ellipsis;
		overflow: hidden;
		white-space: nowrap;
	}
</style>