<template>
	<div class="master-container">
		<div class="card cart">
			<button class="dismiss" type="button" @click="OnCancel">×</button>
			<label class="title">{{title}}</label>
			<div class="products">
				<div class="product">
					<div class="image">
						<img :src="imgSrc" alt="">
					</div>
					<div>
						<span>{{ text }}</span>
						<p>{{ text2 }}</p>
						<p>{{ text3 }}</p>
					</div>
					<div class="quantity">
						<button @click="UpdateAmount(-1)">
							<svg fill="none" viewBox="0 0 24 24" height="14" width="14" xmlns="http://www.w3.org/2000/svg">
								<path stroke-linejoin="round" stroke-linecap="round" stroke-width="2.5" stroke="#47484b" d="M20 12L4 12"></path>
							</svg>
						</button>
						<input type="number" v-model="amountValue" @input="ValidateUpdateAmount" />
						<button @click="UpdateAmount(1)">
							<svg fill="none" viewBox="0 0 24 24" height="14" width="14" xmlns="http://www.w3.org/2000/svg">
								<path stroke-linejoin="round" stroke-linecap="round" stroke-width="2.5" stroke="#47484b" d="M12 4V20M20 12H4"></path>
							</svg>
						</button>
					</div>
					<label class="price small">{{ value }}</label>
				</div>
			</div>
		</div>

		<div class="card checkout">
			<label class="title">{{title2}}</label>
			<div class="details">
				<span>{{ text4 }}</span>
				<span>{{ value2 }}</span>
				<span>{{ text5 }}</span>
				<span>{{ value3 }}</span>
				<span>{{ text6 }}</span>
				<span>{{ value4 }}</span>
			</div>
			<div class="checkout--footer">
				<label class="price">{{ value5 }}</label>
				<button class="checkout-btn" @click="OnConfirm(amountValue)">{{translate.TR_CONFIRM}}</button>
			</div>
		</div>
	</div>
</template>

<script>
	// import Loading from "@/components/Loading.vue";
	export default {
		name: "PopUp",
		// components: { Loading },
		props: {
			title: { default: "" },
			title2: { default: "" },
			imgSrc: { default: "" },

			text: { default: "" },
			text2: { default: "" },
			text3: { default: "" },

			text4: { default: "" },
			text5: { default: "" },
			text6: { default: "" },

			value: { default: "" },
			value2: { default: "" },
			value3: { default: "" },
			value4: { default: "" },
			value5: { default: "" },

			amount: { default: 1 },
			maxAmount: { default: 10 },

			OnConfirm: {},
			OnCancel: {},
			OnInput: {},
		},

		data: function () {
			return {
				amountValue: 1,
				translate: this.$store.state.translate,
			};
		},

		methods: {
			UpdateAmount: function (value) {
				this.amountValue += value;

				this.ValidateUpdateAmount();

				if (this.OnInput) {
					this.OnInput(this.amountValue);
				}
			},

			ValidateUpdateAmount: function () {
				if (Number.isNaN(this.amountValue) || this.amountValue <= 0)
					this.amountValue = 1;
				else if (this.amountValue > this.maxAmount)
					this.amountValue = this.maxAmount;

				if (this.OnInput) {
					this.OnInput(this.amountValue);
				}
			}
		},

		mounted: function () {
			this.amountValue = this.amount;
		},
	};
</script>

<style scoped>
	.master-container {
		display: grid;
		grid-template-columns: auto;
		gap: 5px;

        animation: startAnim 1s;
	}

	.card {
		width: 400px;
		background: #ffffff;
		box-shadow: 0px 187px 75px rgba(0, 0, 0, 0.01),
			0px 105px 63px rgba(0, 0, 0, 0.05), 0px 47px 47px rgba(0, 0, 0, 0.09),
			0px 12px 26px rgba(0, 0, 0, 0.1), 0px 0px 0px rgba(0, 0, 0, 0.1);

		
	}

	@keyframes startAnim {
		0% {
			top: 100%;
			/* transform: translate(-50%, -100%); */
		}
		100% {
			top: 50%;
			transform: translate(-50%, -50%);
		}
	}

	.title {
		width: 100%;
		height: 40px;
		position: relative;
		display: flex;
		align-items: center;
		padding-left: 20px;
		border-bottom: 1px solid #efeff3;
		font-weight: 700;
		font-size: 11px;
		color: #63656b;
	}

	/* cart */
	.cart {
		border-radius: 19px 19px 7px 7px;
	}

	.cart .products {
		display: flex;
		flex-direction: column;
		padding: 10px;
	}

	.cart .products .product {
		display: grid;
		grid-template-columns: 60px 1fr 80px 1fr;
		gap: 10px;
	}

	.cart .products .product span {
		font-size: 13px;
		font-weight: 600;
		color: #47484b;
		margin-bottom: 8px;
		display: block;
	}

	.cart .products .product p {
		font-size: 11px;
		font-weight: 600;
		color: #7a7c81;
	}

	.cart .quantity {
		height: 30px;
		display: grid;
		grid-template-columns: 1fr 1fr 1fr;
		margin: auto;
		background-color: #ffffff;
		border: 1px solid #e5e5e5;
		border-radius: 7px;
		filter: drop-shadow(0px 1px 0px #efefef)
			drop-shadow(0px 1px 0.5px rgba(239, 239, 239, 0.5));
	}

	.cart .quantity input {
		width: 50px;
		height: 30px;
		display: flex;
		align-items: center;
		justify-content: center;
		padding-bottom: 2px;
		font-size: 15px;
		font-weight: 700;
		color: #47484b;
		background: none;
		border: none;
		outline: none;
		text-align: center;
	}

	.cart .quantity button {
		width: 30px;
		height: 30px;
		display: flex;
		align-items: center;
		justify-content: center;
		border: 0;
		outline: none;
		background-color: transparent;
		padding-bottom: 2px;
	}

	.card .small {
		font-size: 15px;
		margin: 0 0 auto auto;
	}

	.card .small sup {
		font-size: px;
	}

	/* coupons */
	.coupons {
		border-radius: 7px;
	}

	.coupons form {
		display: grid;
		grid-template-columns: 1fr 80px;
		gap: 10px;
		padding: 10px;
	}

	.input_field {
		width: auto;
		height: 36px;
		padding: 0 0 0 12px;
		border-radius: 5px;
		outline: none;
		border: 1px solid #e5e5e5;
		filter: drop-shadow(0px 1px 0px #efefef)
			drop-shadow(0px 1px 0.5px rgba(239, 239, 239, 0.5));
		transition: all 0.3s cubic-bezier(0.15, 0.83, 0.66, 1);
	}

	.input_field:focus {
		border: 1px solid transparent;
		box-shadow: 0px 0px 0px 2px #242424;
		background-color: transparent;
	}

	.coupons form button {
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;
		padding: 10px 18px;
		gap: 10px;
		width: 100%;
		height: 36px;
		background: linear-gradient(180deg, #4480ff 0%, #115dfc 50%, #0550ed 100%);
		box-shadow: 0px 0.5px 0.5px #efefef, 0px 1px 0.5px rgba(239, 239, 239, 0.5);
		border-radius: 5px;
		border: 0;
		font-style: normal;
		font-weight: 600;
		font-size: 12px;
		line-height: 15px;
		color: #ffffff;
	}

	/* Checkout */
	.checkout {
		border-radius: 9px 9px 19px 19px;
	}

	.checkout .details {
		display: grid;
		grid-template-columns: 3fr 1fr;
		padding: 10px;
		gap: 5px;
	}

	.checkout .details span {
		font-size: 13px;
		font-weight: 600;
	}

	.checkout .details span:nth-child(odd) {
		font-size: 11px;
		font-weight: 700;
		color: #707175;
		margin: auto auto auto 0;
	}

	.checkout .details span:nth-child(even) {
		font-size: 13px;
		font-weight: 600;
		color: #47484b;
		margin: auto 0 auto auto;
	}

	.checkout .checkout--footer {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 10px 10px 10px 20px;
		background-color: #efeff3;
	}

	.price {
		position: relative;
		font-size: 22px;
		color: #2b2b2f;
		font-weight: 900;
	}

	.price sup {
		font-size: 13px;
	}

	.price sub {
		width: fit-content;
		position: absolute;
		font-size: 11px;
		color: #5f5d6b;
		bottom: 5px;
		display: inline-block;
	}

	.checkout .checkout-btn {
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;
		width: 150px;
		height: 36px;
		background: var(--themed-color);
		box-shadow: 0px 0.5px 0.5px #efefef, 0px 1px 0.5px rgba(239, 239, 239, 0.5);
		border-radius: 7px;
		border: 0;
		outline: none;
		color: #ffffff;
		font-size: 13px;
		font-weight: 600;
		transition: all 0.3s cubic-bezier(0.15, 0.83, 0.66, 1);
	}

	.image {
		background-color: #e9e9e9;
		display: flex;
		align-items: center;
		justify-content: center;

		width: 7vh;
		height: 6vh;
		border-radius: 10px;
	}

	.image img {
		object-fit: cover;

		max-width: 60%;
		max-height: 100%;
	}

	.dismiss {
		position: absolute;
		right: 10px;
		top: 10px;
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 0.5rem 1rem;
		background-color: #fff;
		color: black;
		border: 2px solid #d1d5db;
		font-size: 1rem;
		font-weight: 300;
		width: 25px;
		height: 25px;
		border-radius: 7px;
		transition: 0.3s ease;

		z-index: 1;
	}

	.dismiss:hover {
		background-color: #ee0d0d;
		border: 2px solid #ee0d0d;
		color: #fff;
	}
</style>