import Template02 from '@/views/Template02.vue'
import TemplateTwoItems from '@/components/template02/TemplateTwoItems.vue'
import TemplateTwoCart from '@/components/template02/TemplateTwoCart.vue'
import TemplateTwoMissions from '@/components/template02/TemplateTwoMissions.vue'
import TemplateTwoManage from '@/components/template02/TemplateTwoManage.vue'
import TemplateTwoStock from '@/components/template02/TemplateTwoStock.vue'
import TemplateTwoBalance from '@/components/template02/TemplateTwoBalance.vue'
import TemplateTwoSell from '@/components/template02/TemplateTwoSell.vue'
import TemplateTwoBuy from '@/components/template02/TemplateTwoBuy.vue'
import TemplateTwoName from '@/components/template02/TemplateTwoName.vue'
import TemplateTwoColor from '@/components/template02/TemplateTwoColor.vue'

import AdminMain from '@/views/AdminMain.vue'
import SeeStores from '@/views/SeeStores.vue'
import CreateStore from '@/views/CreateStore.vue'
import NewStore from '@/views/NewStore.vue'
import NewType from '@/views/NewType.vue'
import EditType from '@/views/EditType.vue'

const routes = [
    {
        path: '/',
        name: 'home',
        component: AdminMain
    },
    {
        path: '/admin',
        name: 'admin',
        component: AdminMain
    },
    {
        path: '/admin/see-stores',
        name: "seeStores",
        component: SeeStores
    },
    {
        path: '/admin/create-store',
        name: "createStore",
        component: CreateStore
    },
    {
        path: '/admin/new-store',
        name: "newStore",
        component: NewStore
    },
    {
        path: '/admin/new-type/:id',
        name: "newType",
        props: true,
        component: NewType
    },
    {
        path: '/admin/edit-type',
        name: "editType",
        component: EditType
    },
    {
        path: '/template01',
        name: 'template01',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: Template01,
        children: [
            {
                name: "itemsTemplate01",
                path: "",
                component: TemplateOneItems
            },
            {
                name: "cartTemplate01",
                path: "cart",
                component: TemplateOneCart
            },
            {
                name: "missionsTemplate01",
                path: "missions",
                component: TemplateOneMissions
            },
            {
                name: "manageTemplate01",
                path: "manage",
                component: TemplateOneManage,
            },
            {
                name: "balanceTemplate01",
                path: "manage/balance",
                component: TemplateOneBalance,
            },
            {
                name: "stockTemplate01",
                path: "manage/stock",
                component: TemplateOneStock,
            },
            {
                name: "nameTemplate01",
                path: "manage/name",
                component: TemplateOneName,
            },
            {
                name: "colorTemplate01",
                path: "manage/color",
                component: TemplateOneColor,
            },
            {
                name: "sellTemplate01",
                path: "manage/sell",
                component: TemplateOneSell,
            },
            {
                name: "buyTemplate01",
                path: "manage/buy",
                component: TemplateOneBuy,
            },
            {
                name: "workersTemplate01",
                path: "manage/workers",
                component: TemplateOneWorkers,
            }
        ]
    },
    {
        path: '/template02',
        name: 'template02',
        component: Template02,
        children: [
            {
                name: "itemsTemplate02",
                path: "",
                component: TemplateTwoItems
            },
            {
                name: "cartTemplate02",
                path: "cart",
                component: TemplateTwoCart
            },
            {
                name: "missionsTemplate02",
                path: "missions",
                component: TemplateTwoMissions
            },
            {
                name: "manageTemplate02",
                path: "manage",
                component: TemplateTwoManage,
            },
            {
                name: "balanceTemplate02",
                path: "manage/balance",
                component: TemplateTwoBalance,
            },
            {
                name: "stockTemplate02",
                path: "manage/stock",
                component: TemplateTwoStock,
            },
            {
                name: "nameTemplate02",
                path: "manage/name",
                component: TemplateTwoName,
            },
            {
                name: "colorTemplate02",
                path: "manage/color",
                component: TemplateTwoColor,
            },
            {
                name: "sellTemplate02",
                path: "manage/sell",
                component: TemplateTwoSell,
            },
            {
                name: "buyTemplate02",
                path: "manage/buy",
                component: TemplateTwoBuy,
            },
            {
                name: "workersTemplate02",
                path: "manage/workers",
                component: TemplateOneWorkers,
            }
        ]
    }
]

const router = createRouter({
    history: createWebHashHistory(),
    routes
})

export default router
