<template>
	<div class="container">
        <div v-if="is_loading" class="loading lg-column lg-expand lg-justifyCenter lg-alignCenter">
			<Loading color_primary="rgba(145, 14, 14, 0)" color_secondary="rgba(255, 255, 255, 0.3)" />
		</div>

		<div v-else-if="confirmation" class="confirmation">
			<svg class="checkmark" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 52 52">
				<circle class="checkmark__circle" cx="26" cy="26" r="25" fill="none" />
				<path class="checkmark__check" fill="none" d="M14.1 27.2l7.1 7.2 16.7-16.8" />
			</svg>
		</div>

		<section class="items" v-else>
			<ul class="menu lg-hideScroll">
				<li v-for="(mission, index) in missions" :key="`mission_${index}`" >
					<div class="title">
						<div class="borderTop"></div>
						<div class="bgTitle"></div>
						<p>{{mission.label}}</p>
					</div>
					<div class="img">
						<img :src="`${url_images}${mission.name}.png`" alt="">
					</div>
					<div class="text">
						<div class="line">
							<div>
								<p class="columnText">{{mission.amount}} {{translate.TR_UNITY}}</p>
								<p class="columnSubText">{{translate.TR_AMOUNT_AVAILABLE}}</p>
							</div>
						</div>
						<div class="line">
							<div>
								<p class="columnText">{{translate.TR_SIMBOL_MONEY}} {{mission.payment}}</p>
								<p class="columnSubText">{{translate.TR_WIN_UNITY}}</p>
							</div>
							<div>
								<p class="columnText">{{translate.TR_SIMBOL_MONEY}} {{mission.payment*mission.amount}}</p>
								<p class="columnSubText">{{translate.TR_WIN_TOTAL}}</p>
							</div>
						</div>
						<div class="lineCenter">
							<div class="button">
								<div class="buttonText" @click="DeliverMission(mission)">
									<p>{{translate.TR_DELIVER_MISSION}}</p>
									<i class="fa-solid fa-check"></i>
								</div>
							</div>
						</div>
					</div>
				</li>                
			</ul>
		</section>
	</div>
</template>

<script>
    import axios from 'axios';
    import Loading from "@/components/Loading.vue";

	export default {
		name: "TemplateOneMissions",

        components: {
			Loading,
		},

        data: function(){
            return({
                id: this.$store.state.openned_store.id,
                missions: this.$store.state.openned_store.missions,

                is_loading: false,
                confirmation: false,

                url_images: this.$store.state.url_images,
                translate: this.$store.state.translate,
            });
        },

        methods:{
            DeliverMission: function(mission){
                this.SendToClient('DeliverMission', {
                    id : this.id,
                    mission : mission,
                });

                this.is_loading = true;
            },

            LoadMissions: function(){
                this.SendToClient("LoadMissions", {
                    id: this.id,
                });

                this.is_loading = true;
            },

            SendToClient: function (to, data) {
				axios
					.post(`https://${this.$store.state.name_resource}/${to}`, data)
					.catch((msg) =>
						console.log(
							`An error has occurred (TemplateOneMissions): ${to} : ${msg}`
						)
					);
			},

            ReceiveLua: function (event) {
				if (!event || !event.data) return;
				const lua = event.data;
				if (lua.delivered_mission) {
					this.confirmation = true;
                    this.is_loading = false;

                    this.$store.commit("UpdateMissions", lua.missions);
                    this.missions = this.$store.state.openned_store.missions;

                    setTimeout(() => this.confirmation = false, 3000);
				} else if(lua.cancel_mission){
                    this.is_loading = false;

                    this.$store.commit("UpdateMissions", lua.missions);
                    this.missions = this.$store.state.openned_store.missions;
                } else if(lua.load_missions){
                    this.is_loading = false;

                    this.$store.commit("UpdateMissions", lua.missions);
                    this.missions = this.$store.state.openned_store.missions;
                }
			},
        },

        mounted: function(){
            this.LoadMissions();
        },

        created: function () {
			window.addEventListener("message", this.ReceiveLua);
		},
		unmounted: function () {
			window.removeEventListener("message", this.ReceiveLua);
		},
	};
</script>

<style scoped>
	.container {
		flex: 1;
		width: 150vh;
		margin: auto;
	}

	.items {
		margin-top: 5vh;
		margin-left: 5vh;
		width: 100%;
		overflow: hidden;
	}

	.items ul {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		width: calc(35vh * 3 + 2vh * 3 + 1px * 3);
	}

	.items ul li {
		width: 35vh;
		height: 35vh;

		background: var(--product_background);
		border: 1px solid var(--product_border);

		margin-right: 2vh;
		margin-bottom: 2vh;
	}

	.title {
		position: relative;
	}

	.title .borderTop {
		background: var(--product_border);
		width: 100%;
		height: 0.5vh;
	}

	.title p {
		color: var(--product_text);
		font-size: 1.3vh;
		padding: 0.5vh;
		padding-left: 1vh;
		text-transform: uppercase;
	}

	.title .bgTitle {
		border-right: 5vh solid transparent;
		border-top: 2.8vh solid var(--product_border);
		width: 20vh;
		height: 0;
		position: absolute;
		top: 0.5vh;
	}

	.img {
		margin: auto;
		width: 11vh;
		height: 11vh;
		/* background: gray; */
		margin-top: 0.5vh;
	}

	.img img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	.text {
		/* margin-left: 1vh; */
		display: flex;
		flex-direction: column;
	}

	.text .line {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		padding: 0 1vh;
		margin-bottom: 2vh;
	}

	.text .lineCenter {
		display: flex;
		flex-direction: row;
		justify-content: center;
		margin-top: 3vh;
	}

	.text .columnText {
		font-size: 2vh;
		color: var(--product_text);
	}

	.text .columnSubText {
		font-size: 1vh;
		color: var(--product_text);
        opacity: 0.8;
	}

	.text .button {
		color: var(--text_themed);
		font-size: 2vh;

		position: relative;
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: center;

		width: 100%;
		height: 4vh;
	}
	.text .button .buttonText {
		position: absolute;
		display: flex;
		flex-direction: row;
		width: max-content;

		justify-content: center;

        background: var(--themed-color);
        border-radius: 5px;
        padding: 0.8vh 3vh;
	}
	.text .button .buttonText i {
		margin-left: 1vh;
	}

    .menu{
        height: 80vh;
        overflow-y: scroll;
    }

    .loading, .confirmation {
		margin-top: 20vh;
	}
</style>