<template>
	<div class="container">
		<section class="items">
			<div class="cartTitle">
				<p>{{translate.TR_STORE_CUSTOMIZATION}}</p>
			</div>

			<ul class="menu lg-hideScroll">
				<li v-for="(option, id) in options" :key="`option_${id}`">
					<!-- <div class="title">
						<div class="borderTop"></div>
						<div class="bgTitle"></div>
						<p>{{option.name}}</p>
					</div> -->
					<div class="img">
						<input type="color" v-model="option.color" @input="ChangeColor(option.element, option.color)">
					</div>
					<div class="text">
						<div class="lineCenter">
							<div>
								<p class="columnText">{{option.text}}</p>
								<p class="columnSubText">{{option.subtext}}</p>
							</div>
						</div>
						<div class="lineCenter">
							<div class="button">
								<div class="buttonText" @click="SaveColors">
									<p>{{translate.TR_SAVE}}</p>
								</div>
							</div>
						</div>
					</div>
				</li>
			</ul>
		</section>
	</div>
</template>

<script>
    import axios from "axios";

	export default {
		name: "TemplateOneColor",

		data: function () {
			return {
                id: this.$store.state.openned_store.id,
				options: [
					{
						name: this.$store.state.translate.TR_TITLE_COLOR,
						text: this.$store.state.translate.TR_TEXT_COLOR,
						subtext: this.$store.state.translate.TR_SUBTEXT_COLOR,
                        element: "title",
                        color: "#000000",
					},
					{
						name: this.$store.state.translate.TR_TITLE_COLOR2,
						text: this.$store.state.translate.TR_TEXT_COLOR2,
						subtext: this.$store.state.translate.TR_SUBTEXT_COLOR2_T2,
                        element: "themed_color",
                        color: "#000000",
					},
					{
						name: this.$store.state.translate.TR_TITLE_COLOR3,
						text: this.$store.state.translate.TR_TEXT_COLOR3,
						subtext: this.$store.state.translate.TR_SUBTEXT_COLOR3_T2,
                        element: "text_themed",
                        color: "#000000",
					},
                    {
						name: this.$store.state.translate.TR_TITLE_COLOR7,
						text: this.$store.state.translate.TR_TEXT_COLOR7_T2,
						subtext: this.$store.state.translate.TR_SUBTEXT_COLOR7_T2,
                        element: "nav_border",
                        color: "#000000",
					},
                    {
						name: this.$store.state.translate.TR_TITLE_COLOR9,
						text: this.$store.state.translate.TR_TEXT_COLOR9_T2,
						subtext: this.$store.state.translate.TR_SUBTEXT_COLOR9_T2,
                        element: "nav_border_shadow",
                        color: "#000000",
					},
					{
						name: this.$store.state.translate.TR_TITLE_COLOR4,
						text: this.$store.state.translate.TR_TEXT_COLOR4,
						subtext: this.$store.state.translate.TR_SUBTEXT_COLOR4,
                        element: "product_background",
                        color: "#000000",
					},
					{
						name: this.$store.state.translate.TR_TITLE_COLOR5,
						text: this.$store.state.translate.TR_TEXT_COLOR5,
						subtext: this.$store.state.translate.TR_SUBTEXT_COLOR5,
                        element: "product_text",
                        color: "#000000",
					},
					{
						name: this.$store.state.translate.TR_TITLE_COLOR6,
						text: this.$store.state.translate.TR_TEXT_COLOR6_T2,
						subtext: this.$store.state.translate.TR_SUBTEXT_COLOR6_T2,
                        element: "product_border",
                        color: "#000000",
					},
                    {
						name: this.$store.state.translate.TR_TITLE_COLOR8,
						text: this.$store.state.translate.TR_TEXT_COLOR8_T2,
						subtext: this.$store.state.translate.TR_SUBTEXT_COLOR8_T2,
                        element: "nav_border_selected",
                        color: "#000000",
					},
				],

                translate: this.$store.state.translate,
			};
		},

		methods: {
            ChangeColor: function(element, color){
                this.$store.commit("UpdateColors", {element, color});
            },
            SaveColors: function(){
                this.SendToClient("SaveColors", {
                    id: this.id,
                    colors: this.$store.state.openned_store.colors,
                })
            },
			SendToClient: function (to, data) {
				axios
					.post(`https://${this.$store.state.name_resource}/${to}`, data)
					.catch((msg) =>
						console.log(
							`An error has occurred (TemplateTwoColor): ${to} : ${msg}`
						)
					);
			},
		},

		mounted: function () {
            const colors = this.$store.state.openned_store.colors;
            this.options.forEach(option => {
                option.color = colors[option.element];
            });
        },
	};
</script>

<style scoped>
	.container {
        /* 
            --nav_border_selected: #f1f1f1
            --nav_border: #323c67
            --product_background: white
            --product_border: #a2a2a280
            --nav_border_shadow: #E9E9E9
            --product_text: #323C67
            --text_themed: white
            --themed-color: #03b67a        
        */
        
		/* background-color: #f1f1f1; */
        background-color: var(--nav_border_selected);
		margin-top: -0.1vh;
		flex: 1;
		padding: 2vh 5vh;
	}

    .cartTitle {
		text-align: center;
		font-size: 5vh;
		font-weight: bolder;
		color: var(--nav_border);
        /* color: #323c67; */
	}

	.items {
		/* margin-top: 5vh; */
		padding-left: 5vh;
		width: 100%;
		overflow: hidden;
	}

	.items ul {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		width: 100%;
        margin: auto;
        margin-top: 2vh;

        height: 50vh;
        overflow-y: scroll;

        /* background: black; */
	}

	.items ul li {
		width: calc(100% / 4 - 2vh + 2vh / 4);
		height: 30vh;

		background-color: var(--product_background);
        box-shadow: 1px 1px 3px 1px var(--product_border);
        /* box-shadow: 1px 1px 3px 1px #a2a2a280; */

        /* 86868665 */
		margin-bottom: 2vh;
	}

	.items ul li:not(:nth-of-type(4n)) {
		margin-right: 2vh;
	}

	.img {
		width: 97%;
        height: 55%;
        margin: auto;
        margin-top: 0.5vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: var(--nav_border_shadow);
        /* background-color: #E9E9E9; */
	}

	.img input {
		margin-top: 2vh;
		width: 10vh;
		height: 10vh;

		border-radius: 50%;
	}

	.text {
		/* margin-left: 1vh; */
		display: flex;
		flex-direction: column;
	}

	.text .line {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		padding: 0 1vh;
		margin-bottom: 2vh;
		margin-top: 2vh;
	}

	.text .lineCenter {
		display: flex;
		flex-direction: row;
		justify-content: center;
		margin-top: 1vh;
	}

	.text .columnText {
		/* color: #323C67; */
        color: var(--product_text);
        font-weight: 500;
        text-align: center;
        margin-top: 1vh;
        font-size: 1.5vh;
	}

	.text .columnSubText {
		/* color: #323C67; */
        color: var(--product_text);
        font-weight: 500;
        text-align: center;
        font-size: 1vh;
        opacity: 0.7;
	}

	.text .lineCenter .columnSubText,
	.text .lineCenter .columnText {
		text-align: center;
	}

	.text .button {
		color: var(--text_themed);
		font-size: 2vh;

		position: relative;
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: center;

		width: 100%;
		height: 4vh;
	}
	.text .button .buttonText {
		position: absolute;
		display: flex;
		flex-direction: row;
		width: max-content;

		justify-content: center;
		background: var(--themed-color);
		/* border-radius: 5px; */
		padding: 0.8vh 3vh;
	}
	.text .button .buttonText i {
		margin-left: 1vh;
	}

    .menu{
        height: 80vh;
        overflow-y: scroll;
    }
</style>