<template>
	<div class="container">
		<PopUp class="popup" v-if="pop_up.openned" :title="pop_up.title" :title2="pop_up.title2" :amount="pop_up.amount" :maxAmount="pop_up.maxAmount" :imgSrc="pop_up.imgSrc" :text="pop_up.text" :text2="pop_up.text2" :text3="pop_up.text3" :text4="pop_up.text4" :text5="pop_up.text5" :text6="pop_up.text6" :value="pop_up.value" :value2="pop_up.value2" :value3="pop_up.value3" :value4="pop_up.value4" :value5="pop_up.value5" :OnConfirm="pop_up.func_confirm" :OnCancel="pop_up.func_cancel" :OnInput="pop_up.func_input" />
		<PopUp2 class="popup" v-if="pop_up.openned2" :title="pop_up.title" :text="pop_up.text" :loading="pop_up.loading" />
		<section class="items column">
			<div class="manageTitle">
				<p>{{translate.TR_STORE_STOCK}}</p>
			</div>

            <div v-if="is_loading" class="loading lg-column lg-expand lg-justifyCenter lg-alignCenter">
                <Loading color_primary="rgba(145, 14, 14, 1)" color_secondary="rgba(255, 255, 255, 0.8)" />
            </div>

			<div class="cartBody" v-else >
				<div class="cartItems">
					<p class="cartItemsTitle">{{translate.TR_STORE_PRODUCTS_IN_STOCK}}</p>

					<ul class="menu lg-hideScroll">
						<li v-for="(product, index) in stock" :key="`stock_${index}`">
							<div class="title">
								<div class="img">
									<img :src="`${url_images}${product.name}.png`" alt="">
								</div>

								<p>{{product.label}}</p>
							</div>

							<p class="cartWeight" v-if="weight_system">{{product.weight}} {{translate.TR_SIMBOL_WEIGHT}}</p>

							<div class="cartAmount">
								<p>x</p>
								<p>{{product.amount}}</p>
							</div>

							<p class="cartItemRemove" @click="PopUpPutForSale(index)">{{translate.TR_PUT_SALE}}</p>
							<!-- <p class="cartItemRemove" @click="PopUpMission(index)">{{translate.TR_CREATE_MISSION}}</p> -->
						</li>
					</ul>
				</div>

				<div class="cartItems">
					<p class="cartItemsTitle">{{translate.TR_PRODUCTS_STORE}}</p>

					<ul class="menu lg-hideScroll">
						<li v-for="(product, index) in products" :key="`product_${index}`">
							<div class="title">
								<div class="img">
									<img :src="`${url_images}${product.name}.png`" alt="">
								</div>

								<p>{{product.label}}</p>
							</div>

							<div class="cartAmount">
								<p>x</p>
								<p>{{product.amount}}</p>
							</div>

							<p class="cartItemprice">{{translate.TR_SIMBOL_MONEY}} {{product.price.toFixed(2)}}</p>

							<p class="cartItemRemove" @click="PopUpRemove(index)">{{translate.TR_REMOVE}}</p>
							<p class="cartItemRemove" @click="PopUpPrice(index)">{{translate.TR_CHANGE_PRICE}}</p>
						</li>
					</ul>
				</div>
			</div>
		</section>
	</div>
</template>

<script>
	import axios from "axios";
	import Loading from "@/components/Loading2.vue";
	import PopUp from "@/components/PopUp3.vue";
	import PopUp2 from "@/components/PopUp4.vue";
	export default {
		name: "TemplateOneItems",

		components: {
			Loading,
			PopUp,
			PopUp2,
		},

		data: function () {
			return {
				id: this.$store.state.openned_store.id,
				stock: [
					{
						name: "buy",
						label: "GoldBar",
						price: 5000,
						amount: 50,
						weight: 1,
					},
				],
				products: [
					{
						name: "buy",
						label: "GoldBar",
						price: 5000,
						amount: 50,
						weight: 1,
					},
				],
				products_mission: {
					['buy']: {
						name: "pen",
						label: "Pen",
						price_mission: 10.0,
						max_request: 20,
						payment: 100,
					}
				},

				is_loading: false,

				pop_up: {
					openned: false,
					openned2: false,
					title: "Create Mission",
					title2: "Necessário para completar missão",
					imgSrc: "img/buy.png",

					text: "Cheese Burger",
					text2: "Item solicitado",
					text3: "Verifique abaixo",

					text4: "Este é o item necessário para a missão:",
					text5: "Quem entregar a missão, ganhará:",
					text6: "Para criar essa missão, sua loja pagará:",

					value: "$23.99",
					value2: "$47.99",
					value3: "$3.99",
					value4: "$4.99",
					value5: "$57.99",

					amount: 1,
					maxAmount: 10,

					loading: false,

					func_confirm: false,
					func_cancel: this.PopUpCancel,
				},

				weight_system: this.$store.state.weight_system,
				url_images: this.$store.state.url_images,
				translate: this.$store.state.translate,
			};
		},

		methods: {
			Confirm: function () {
				// 
			},

			PopUpMission: function (index) {
				if (this.pop_up.openned || this.pop_up.openned2)
					return;

				this.pop_up.title = this.translate.TR_CREATE_MISSION;
				this.pop_up.title2 = this.translate.TR_REQUIRED_COMPLETE;
				this.pop_up.imgSrc = `${this.url_images}${this.stock[index].name}.png`;
				this.pop_up.text = this.stock[index].label;
				this.pop_up.text2 = this.translate.TR_ITEM_REQUESTED;
				this.pop_up.text3 = this.translate.TR_CHECK_BELOW;
				this.pop_up.text4 = this.translate.TR_ITEM_NEEDED;
				this.pop_up.text5 = this.translate.TR_WILL_EARN;
				this.pop_up.text6 = this.translate.TR_TO_CREATE_MISSION;
				this.pop_up.value = `${this.translate.TR_SIMBOL_MONEY}${this.products_mission[this.stock[index].name].price_mission}`;
				this.pop_up.value2 = this.products_mission[this.stock[index].name].label;
				this.pop_up.value3 = `${this.translate.TR_SIMBOL_MONEY}${this.products_mission[this.stock[index].name].payment}`;
				this.pop_up.value4 = `${this.translate.TR_SIMBOL_MONEY}${this.products_mission[this.stock[index].name].price_mission}`;
				this.pop_up.value5 = `${this.translate.TR_SIMBOL_MONEY}${this.products_mission[this.stock[index].name].price_mission}`;

                this.pop_up.amount = 1;
				this.pop_up.maxAmount = this.products_mission[this.stock[index].name].max_request;
				this.pop_up.openned = true;

				this.pop_up.func_input = (amount) => {
					amount = Number(amount);

					if (!Number.isNaN(amount) && amount > 0) {
						amount = Math.floor(amount);

						if (amount > this.products_mission[this.stock[index].name].max_request) {
                            this.pop_up.value3 = `${this.translate.TR_SIMBOL_MONEY}${this.products_mission[this.stock[index].name].payment * this.products_mission[this.stock[index].name].max_request}`;
							this.pop_up.value4 = `${this.translate.TR_SIMBOL_MONEY}${this.products_mission[this.stock[index].name].price_mission * this.products_mission[this.stock[index].name].max_request}`;
							this.pop_up.value5 = `${this.translate.TR_SIMBOL_MONEY}${this.products_mission[this.stock[index].name].price_mission * this.products_mission[this.stock[index].name].max_request}`;
						} else {
                            this.pop_up.value3 = `${this.translate.TR_SIMBOL_MONEY}${this.products_mission[this.stock[index].name].payment * amount}`;
							this.pop_up.value4 = `${this.translate.TR_SIMBOL_MONEY}${this.products_mission[this.stock[index].name].price_mission * amount}`;
							this.pop_up.value5 = `${this.translate.TR_SIMBOL_MONEY}${this.products_mission[this.stock[index].name].price_mission * amount}`;
						}
					}
				};

				this.pop_up.func_confirm = (amount) => {
					amount = Number(amount);

					if (!Number.isNaN(amount) && amount > 0) {
						amount = Math.floor(amount);

						this.SendToClient("CreateMission", {
							id: this.id,
							product: this.stock[index],
							amount: amount,
						});

						this.PopUpCancel();
						this.PopUpWait();
					}
				};
			},

			PopUpPutForSale: function (index) {
				if (this.pop_up.openned || this.pop_up.openned2)
					return;

				this.pop_up.title = this.translate.TR_PUT_SALE_AMOUNT;
				this.pop_up.title2 = this.translate.TR_PUT_SALE;
				this.pop_up.imgSrc = `${this.url_images}${this.stock[index].name}.png`;
				this.pop_up.text = this.stock[index].label;
				this.pop_up.text2 = this.translate.TR_ITEM_REQUESTED;
				this.pop_up.text3 = "";
				this.pop_up.text4 = "";
				this.pop_up.text5 = "";
				this.pop_up.text6 = "";
				this.pop_up.value = "";
				this.pop_up.value2 = "";
				this.pop_up.value3 = "";
				this.pop_up.value4 = "";
				this.pop_up.value5 = "";

                this.pop_up.amount = 1;
				this.pop_up.maxAmount = this.stock[index].amount;
				this.pop_up.openned = true;

				this.pop_up.func_input = null;

				this.pop_up.func_confirm = (amount) => {
					amount = Number(amount);

					if (!Number.isNaN(amount) && amount > 0) {
						amount = Math.floor(amount);

						this.SendToClient("PutForSaleProduct", {
							id: this.id,
							product: this.stock[index],
							amount: amount,
						});

						this.PopUpWait();
					}
				};
			},

			PopUpRemove: function (index) {
				if (this.pop_up.openned || this.pop_up.openned2)
					return;

				this.pop_up.title = this.translate.TR_REMOVE_AMOUNT;
				this.pop_up.title2 = this.translate.TR_REMOVE;
				this.pop_up.imgSrc = `${this.url_images}${this.products[index].name}.png`;
				this.pop_up.text = this.products[index].label;
				this.pop_up.text2 = this.translate.TR_ITEM_REQUESTED;
				this.pop_up.text3 = "";
				this.pop_up.text4 = "";
				this.pop_up.text5 = "";
				this.pop_up.text6 = "";
				this.pop_up.value = "";
				this.pop_up.value2 = "";
				this.pop_up.value3 = "";
				this.pop_up.value4 = "";
				this.pop_up.value5 = "";

                this.pop_up.amount = 1;
				this.pop_up.maxAmount = this.products[index].amount;
				this.pop_up.openned = true;

				this.pop_up.func_input = null;

				this.pop_up.func_confirm = (amount) => {
					amount = Number(amount);

					if (!Number.isNaN(amount) && amount > 0) {
						amount = Math.floor(amount);

						this.SendToClient("RemoveProduct", {
							id: this.id,
							product: this.products[index],
							amount: amount,
						});

						this.PopUpWait();
					}
				};
			},

			PopUpPrice: function (index) {
				if (this.pop_up.openned || this.pop_up.openned2)
					return;

				this.pop_up.title = this.translate.TR_NEW_PRICE;
				this.pop_up.title2 = this.translate.TR_CHANGE_PRICE;
				this.pop_up.imgSrc = `${this.url_images}${this.products[index].name}.png`;
				this.pop_up.text = this.products[index].label;
				this.pop_up.text2 = this.translate.TR_ITEM_REQUESTED;
				this.pop_up.text3 = "";
				this.pop_up.text4 = "";
				this.pop_up.text5 = "";
				this.pop_up.text6 = "";
				this.pop_up.value = "";
				this.pop_up.value2 = "";
				this.pop_up.value3 = "";
				this.pop_up.value4 = "";
				this.pop_up.value5 = "";

                this.pop_up.amount = this.products[index].price;
				this.pop_up.maxAmount = this.products[index].max_price;

				this.pop_up.subtexts = [
					`${this.translate.TR_CURRENT}: ${this.products[index].price}`,
					`${this.translate.TR_MAX_PRICE} ${this.products[index].max_price}`,
				];
				this.pop_up.openned = true;

				this.pop_up.func_input = null;

				this.pop_up.func_confirm = (new_price) => {
					new_price = Number(new_price);

					if (!Number.isNaN(new_price) && new_price > 0) {
						new_price = Math.floor(new_price);

						this.SendToClient("ChangePriceProduct", {
							id: this.id,
							product: this.products[index],
							new_price: new_price,
						});

						this.PopUpWait();
					}
				};
			},

			PopUpWait: function () {
				this.PopUpCancel();

				this.pop_up.openned = false;
				this.pop_up.openned2 = true;
				this.pop_up.title = this.translate.TR_WAIT;
				this.pop_up.text = "";
				this.pop_up.loading = true;
			},

			PopUpCancel: function () {
				this.pop_up.openned = false;
				this.pop_up.openned2 = false;
				this.pop_up.func_confirm = this.PopUpCancel;
				this.pop_up.func_input = false;
			},

			LoadStock: function () {
				this.is_loading = true;
				this.SendToClient("GetStoreStock", {
					id: this.$store.state.openned_store.id,
				});
			},

			SendToClient: function (to, data) {
				axios
					.post(`https://${this.$store.state.name_resource}/${to}`, data)
					.catch((msg) =>
						console.log(
							`An error has occurred (TemplateTwoStock): ${to} : ${msg}`
						)
					);
			},

			ReceiveLua: function (event) {
				if (!event || !event.data) return;
				const lua = event.data;
				if (lua.receive_stock) {
					this.products = lua.products;
					this.stock = lua.stock;
					this.products_mission = lua.products_mission;
					this.is_loading = false;
					this.PopUpCancel();

					this.$store.commit("UpdateProducts", this.products);
					this.$store.commit("SetCart", []);
				} else if (lua.created_mission) {
					this.pop_up.loading = false;
					this.pop_up.title = this.translate.TR_OPERATION_SUCCESS;
					this.pop_up.text = "";

					this.$store.commit("UpdateMissions", lua.missions);

					setTimeout(() => {
						this.PopUpCancel();
					}, 3000);
				}
			},
		},

		created: function () {
			window.addEventListener("message", this.ReceiveLua);

			this.LoadStock();
		},
		unmounted: function () {
			window.removeEventListener("message", this.ReceiveLua);
		},
	};
</script>

<style scoped>
	.container {
		background-color: #f1f1f1;
		margin-top: -0.1vh;
		flex: 1;
		padding: 2vh 5vh;
	}

	.manageTitle {
		text-align: center;
		font-size: 5vh;
		font-weight: bolder;
		color: #323c67;
	}

	.items {
		/* overflow-y: scroll; */
		max-height: 48vh;
		margin-top: 1vh;
	}

	.cartTitle {
		text-align: center;
		font-size: 5vh;
		font-weight: bolder;
		color: #323c67;
	}

	.cartBody {
		display: flex;
		flex-direction: row;
		justify-content: center;

		margin-top: 2vh;
	}

	.cartItems {
		background-color: white;
		flex: 1;
		margin-left: 5vh;
		padding: 3vh;
		border-radius: 5px;
		box-shadow: 0px 0px 3px 1px rgba(173, 173, 173, 0.315);
	}

	.cartItemsTitle {
		color: gray;
		font-weight: bold;
		font-size: 2vh;
	}

	.cartItems ul {
		display: flex;
		flex-direction: column;
		margin-top: 2vh;
		height: 30vh;

		overflow-y: scroll;
	}

	.cartItems li {
		display: flex;
		flex-direction: row;

		align-items: center;
		justify-content: space-between;

		border-bottom: 1px solid rgb(212, 212, 212);
		padding: 1vh 0;

		color: gray;
		font-weight: bold;
		font-size: 2.2vh;
	}

	.cartItems li:first-child {
		border-top: 1px solid rgb(212, 212, 212);
	}

	.title {
		display: flex;
		flex-direction: row;

		align-items: center;
		width: 35%;

		text-overflow: ellipsis;
		overflow: hidden;
		white-space: nowrap;
	}

	.title p {
		margin-left: 1vh;
		overflow: hidden;
		/* background-color: tomato; */
		flex: 1;

		text-overflow: ellipsis;
	}

	.img {
		background-color: #e9e9e9;
		display: flex;
		align-items: center;
		justify-content: center;

		width: 10vh;
		height: 6vh;
	}

	.img img {
		/* width: 100%;
															height: 100%; */
		object-fit: cover;

		max-width: 40%;
		max-height: 100%;
	}

	.cartWeight {
		/* width: 25%; */
		/* background-color: wheat; */
		text-align: center;
	}

	.cartAmount {
		/* width: 25%; */
		/* background-color: wheat; */
		text-align: center;

		text-overflow: ellipsis;
		overflow: hidden;
		white-space: nowrap;

		display: flex;
		flex-direction: row;
	}

	.cartItemRemove {
		display: none;
		color: white;
		padding: 1vh 2vh;
		border-radius: 20px;
		font-size: 2vh;

		background-color: #03b67a;

		display: none;
	}

	.cartItems li:hover .cartItemRemove {
		display: block;
	}

	.cartItems li:hover .cartAmount,
	.cartItems li:hover .cartWeight,
	.cartItems li:hover .cartItemprice {
		display: none;
	}

	.popup {
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		z-index: 1;
	}

    .loading{
        height: 30vh;
    }
</style>