<template>
	<div>
		<div class="container lg-column" v-if="!is_hide">
			<div class="title lg-line lg-alignCenter" @click="() => $router.back()">
				<i class="fa-solid fa-caret-left"></i>
				<h1>{{translate.TR_NEWSTORE}}</h1>
			</div>

			<div class="bodyMenu lg-column">
				<div class="menu lg-column lg-expand" v-if="stage == 0">
					<p class="menuTitle">{{translate.TR_NAMESTORE}}</p>

					<input type="text" :placeholder="translate.TR_TYPE_HERE" v-model="information.name">

					<ul class="lg-column lg-expand">
						<li class="lg-line lg-justifyBetween lg-alignCenter" @click="ChangeStage(1)">
							<p>{{translate.TR_CONFIRM}}</p>
							<i class="fa-solid fa-chevron-right"></i>
						</li>
					</ul>
				</div>

				<div class="menu lg-column lg-expand" v-if="stage == 1">
					<p class="menuTitle">{{translate.TR_WHAT_TYPE}}</p>

					<ul class="lg-column lg-expand">
						<li class="lg-line lg-justifyBetween lg-alignCenter" v-for="(type, index) in store_types" :key="`type_${index}`" @click="SetType(type.id)">
							<p class="lg-uppercase">{{type.information.name}}</p>
							<i class="fa-solid fa-chevron-right"></i>
						</li>
					</ul>
				</div>

				<div class="menu lg-column lg-expand" v-if="stage == 2">
					<p class="menuTitle">{{translate.TR_COORDS_TEXT}}</p>
					<p class="menuTitle">{{translate.TR_COORDS}}</p>

					<input type="text" placeholder="vector3(X, Y, Z)" v-model="information.marker_coords">
					<input type="text" placeholder="scale: 0.5" v-model="information.marker_scale">

					<ul class="lg-column lg-expand">
						<li class="lg-line lg-justifyBetween lg-alignCenter" @click="ChooseCoords">
							<p>{{translate.TR_HIDE_NUI}}</p>
							<i class="fa-solid fa-chevron-right"></i>
						</li>
						<li class="lg-line lg-justifyBetween lg-alignCenter" @click="ChangeStage(1)">
							<p>{{translate.TR_CONFIRM}}</p>
							<i class="fa-solid fa-chevron-right"></i>
						</li>
					</ul>
				</div>

				<!-- <div class="menu lg-column lg-expand" v-if="stage == 3">
					<p class="menuTitle">{{translate.TR_MONEYBOX_TEXT}}</p>
					<p class="menuTitle">{{translate.TR_MONEYBOX}}</p>

					<input type="text" placeholder="HASH OBJECT" v-model="information.moneybox_hash">
					<input type="text" placeholder="VECTOR3(X,Y,Z)" v-model="information.moneybox_coords">

					<ul class="lg-column lg-expand">
						<li class="lg-line lg-justifyBetween lg-alignCenter" @click="ChooseMoneyBox">
							<p>{{translate.TR_HIDE_NUI}}</p>
							<i class="fa-solid fa-chevron-right"></i>
						</li>
						<li class="lg-line lg-justifyBetween lg-alignCenter" @click="ChangeStage(1)">
							<p>{{translate.TR_CONFIRM}}</p>
							<i class="fa-solid fa-chevron-right"></i>
						</li>
					</ul>
				</div> -->

                <div class="menu lg-column lg-expand" v-if="stage == 3">
					<p class="menuTitle">{{translate.TR_COST_STORE}}</p>

					<input type="text" :placeholder="translate.TR_TYPE_VALUE" v-model.number="information.price_buy">

					<ul class="lg-column lg-expand">
						<li class="lg-line lg-justifyBetween lg-alignCenter" @click="ChangeStage(1)">
							<p>{{translate.TR_CONFIRM}}</p>
							<i class="fa-solid fa-chevron-right"></i>
						</li>
					</ul>
				</div>

                <div class="menu lg-column lg-expand" v-if="stage == 4">
					<p class="menuTitle">{{translate.TR_GET_SELL}}</p>

					<input type="text" :placeholder="translate.TR_TYPE_VALUE" v-model.number="information.price_sell">

					<ul class="lg-column lg-expand">
						<li class="lg-line lg-justifyBetween lg-alignCenter" @click="ChangeStage(1)">
							<p>{{translate.TR_CONFIRM}}</p>
							<i class="fa-solid fa-chevron-right"></i>
						</li>
					</ul>
				</div>
			</div>
		</div>

		<div class="container lg-column" v-else>
			<div class="centralize">
				<p>{{text1}}</p>
				<p>{{text2}}</p>
				<p>{{text3}}</p>
			</div>
			<div class="instruction">

			</div>
		</div>
	</div>
</template>

<script>
	import axios from "axios";

	export default {
		data: function () {
			return {
				id: 0,
				information: {
					type: 0,
					name: "",
					marker_coords: "",
					marker_scale: "",
					moneybox_coords: "",
					moneybox_hash: "",
                    price_buy: "",
                    price_sell: "",
				},
				store_types: [
					{
						id: 0,
						information: {
							name: "Supermarket",
						},
					},
					{
						id: 1,
						information: {
							name: "Ammunation",
						},
					},
				],

				text1: "",
				text2: "",
				text3: "",

				operation: "creating",
				is_basic: false,

				stage: 0,
				is_hide: false,
				translate: this.$store.state.translate,
			};
		},

		methods: {
			ChangeStage: function (amount) {
				this.stage += amount;
				this.search_item_server = "";
				this.search_products = "";

				if (this.stage > 4) {
					this.SendToClient("SaveStore", {
						id: this.id,
						information: this.information,
						operation: this.operation,
						is_basic: this.is_basic,
					});

					this.$router.back();
				}
			},

			SetType: function (id) {
				this.information.type = id;
				this.ChangeStage(1);
			},

			ChooseCoords: function () {
				this.text1 = this.translate.TR_USE_SCROLL;
				this.text2 = this.translate.TR_USE_ARROW;
				this.text3 = this.translate.TR_CONFIRM_SELECTION;
				this.is_hide = true;

                const store_type = this.store_types.filter(element => element.id == this.information.type)[0];
				this.SendToClient("ChooseCoords", {
                    marker: store_type.information.marker,
                    marker_color: store_type.information.marker_color,
                });
			},

			ChooseMoneyBox: function () {
				this.text1 = this.translate.TR_USE_AIM;
				this.text2 = this.translate.TR_USE_WEAPON;
				this.text3 = this.translate.TR_CONFIRM_SELECTION;
				this.is_hide = true;
				this.SendToClient("ChooseMoneyBox", {});
			},

			Initializate: function () {
				this.SendToClient("LoadTypes", {});
			},

			SendToClient: function (to, data) {
				axios
					.post(`https://${this.$store.state.name_resource}/${to}`, data)
					.catch((msg) =>
						console.log(`An error has occurred (NewType): ${to} : ${msg}`)
					);
			},
			ReceiveLua: function (event) {
				if (!event || !event.data) return;
				const lua = event.data;
				if (lua.hide) {
					this.is_hide = true;
				} else if (lua.set_coords) {
					this.is_hide = false;
					this.information.marker_coords = lua.coords;
					this.information.marker_scale = lua.scale;
				} else if (lua.set_moneybox) {
					this.is_hide = false;
					this.information.moneybox_coords = lua.coords;
					this.information.moneybox_hash = lua.hash;
				} else if (lua.load_types) {
					this.store_types = lua.store_types;
				}
			},
		},
		created: function () {
			window.addEventListener("message", this.ReceiveLua);
			this.Initializate();
		},
		unmounted: function () {
			window.removeEventListener("message", this.ReceiveLua);
		},
	};
</script>

<style scoped>
	.container {
		width: 111vh;
		margin: auto;
	}

	.title {
		color: rgb(228, 51, 51);
		margin-top: 5vh;
		font-size: 1.8vh;
	}

	.title i {
		font-size: 3vh;
		margin-right: 1vh;
	}

	.bodyMenu {
		margin-top: 5vh;
	}

	.menu:not(:last-child) {
		margin-right: 1vh;
	}

	.menuTitle {
		color: rgba(255, 255, 255, 0.4);
		padding: 0.5vh 0;
	}

	.menu li {
		background: rgba(255, 255, 255, 0.1);
		padding: 1vh;
		color: white;
		border: 1px solid rgba(128, 128, 128, 0.2);
		margin-bottom: 0.5vh;
		/* transition: 0.3s all; */
	}
	.menu li:hover {
		background: rgba(255, 255, 255, 0.2);
		border: 1px solid rgba(128, 128, 128, 1);
	}

	.menu li.selected {
		background: rgba(241, 0, 0, 0.5);
	}

	.menu input {
		padding: 1vh;
		background: rgba(156, 156, 156, 0.1);
		border: none;
		margin-bottom: 1vh;
		color: rgba(255, 255, 255, 0.616);
		font-size: 1.8vh;
		outline: none;
		/* border-radius: 5px; */
		text-transform: uppercase;
		text-align: center;
	}

	.menuInfo div:first-of-type {
		margin-top: 1vh;
	}

	.menuInfo p {
		color: rgba(255, 255, 255, 0.3);
	}

	.menuInfo > p {
		color: white;
	}

	.menuFinal {
		padding-left: 2vh;
	}

	.menuFinal h2 {
		color: rgb(228, 51, 51);
	}

	.menuFinal h3 {
		color: rgb(255, 255, 255);
		font-weight: 500;
		text-transform: uppercase;
	}

	.menuFinal > div {
		margin: 2vh 0;
	}

	.menuFinal div:first-of-type {
		/* margin-top: 8vh; */
	}

	.menuFinal p {
		font-size: 2vh;
		color: rgba(255, 255, 255, 0.795);
	}

	.menuFinal .button {
		background: rgba(255, 255, 255, 0.1);
		padding: 1vh;
		color: white;
		border: 1px solid rgba(128, 128, 128, 0.2);
		margin-bottom: 0.5vh;
	}

	.menuFinal .button:hover {
		background: rgba(255, 255, 255, 0.2);
		border: 1px solid rgba(128, 128, 128, 1);
	}

	.centralize {
		position: absolute;
		left: 50%;
		top: 70%;
		transform: translate(-50%, -70%);
		font-size: 2.5vh;
		color: rgba(255, 255, 255, 0.295);
		text-align: center;
		text-transform: uppercase;
	}
</style>