<template>
	<div class="container lg-line lg-alignCenter">
		<section class="items">
			<div class="box">
				<div class="title">
					<div class="borderTop"></div>
					<div class="bgTitle"></div>
					<p>{{translate.TR_NAME_STORE}}</p>
				</div>
				<div class="back" @click="() => $router.back()">
					<i class="fa-solid fa-caret-left"></i>
					<p>{{translate.TR_BACK}}</p>
				</div>

                <div class="lg-column lg-expand lg-alignCenter">
                    <div class="img">
                        <img src="img/renomear.png" alt="">
                    </div>
                    <p class="text" >{{translate.TR_NAME_TEXT}}</p>
                </div>

				<div class="lg-line lg-expand">
                    <div class="lg-column lg-expand">
                        <input class="inputValue lg-expand" type="text" :placeholder="translate.TR_TYPE_HERE" v-model="name" >
                        
                        <p class="button lg-button-transparent lg-text-white" @click="Confirm" >{{translate.TR_CONFIRM}} </p>
                    </div>
                </div>
			</div>
		</section>
	</div>
</template>

<script>
    import axios from "axios";

	export default {
		name: "TemplateOneItems",

		data: function () {
			return {
                id: this.$store.state.openned_store.id,
                name: this.$store.state.openned_store.name,
                translate: this.$store.state.translate,
            };
		},

		methods: {
            Confirm: function(){
                this.$store.state.openned_store.name = this.name;
                this.SendToClient("ChangeStoreName", {
                    id: this.id,
                    name: this.name,
                })

            },
            SendToClient: function (to, data) {
				axios
					.post(`https://${this.$store.state.name_resource}/${to}`, data)
					.catch((msg) =>
						console.log(`An error has occurred (TemplateOneName): ${to} : ${msg}`)
					);
			},
        },
	};
</script>

<style scoped>
	.container {
		flex: 1;
		width: 150vh;
		margin: auto;

        /* background: wheat; */
	}

	.items {
		/* margin-top: 5vh; */
		margin-left: 5vh;
		width: 100%;
		overflow: hidden;

        /* background: whitesmoke; */
	}

	.items .box {
		width: 111vh;
		height: 45vh;

		background: var(--product_background);
		border: 1px solid var(--product_border);

		margin-bottom: 2vh;

        flex: 1;
	}

	.title {
		position: relative;
	}

	.title .borderTop {
		background: var(--product_border);
		width: 100%;
		height: 0.5vh;
	}

	.title p {
		color: white;
		font-size: 1.3vh;
		padding: 0.5vh;
		padding-left: 1vh;
		text-transform: uppercase;
	}

	.title .bgTitle {
		border-right: 5vh solid transparent;
		border-top: 2.8vh solid var(--product_border);
		width: 20vh;
		height: 0;
		position: absolute;
		top: 0.5vh;
	}

	.back {
		display: flex;
		flex-direction: row;
		align-items: center;
		color: rgba(255, 255, 255, 0.596);
		font-size: 2vh;
		padding: 2vh 1vh;
	}
	.back i {
		margin-right: 1vh;
	}

    .img{
        width: 10vh;
        height: 10vh;
        
    }

    .img img{
        width: 100%;
        object-fit: cover;
    }

    .text{
        margin-bottom: 3vh;
        color: rgba(190, 190, 190, 0.753);
        font-size: 1.8vh;
    }

    .inputValue{
        background: rgba(255, 255, 255, 0.05);
        border: none;
        outline: none;
        font-size: 2vh;
        padding: 1vh;
        margin: auto;
        text-align: center;
        margin-bottom: 2vh;
        width: 90%;
        color: white;
        border-radius: 5px;
    }

    .button{
        margin: auto;
    }
</style>