<template>
	<div class="container">
		<section class="items">
			<div class="box lg-column">
				<div class="title">
					<div class="borderTop"></div>
					<div class="bgTitle"></div>
					<p>{{translate.TR_BALANCE}}</p>
				</div>
				<div class="back" @click="() => $router.back()">
					<i class="fa-solid fa-caret-left"></i>
					<p>{{translate.TR_BACK}}</p>
				</div>

                <div v-if="is_loading" class="loading lg-column lg-expand lg-justifyCenter lg-alignCenter" >
                    <Loading color_primary="rgba(255, 255, 255, 0)" color_secondary="rgba(145, 14, 14, 1)" />
                </div>

                <div v-else >
                    <div class="img">
                        <img src="img/salary.png" alt="">
                    </div>
                    <div class="text">
                        <div class="lineCenter">
                            <div>
                                <p class="columnText">{{translate.TR_SIMBOL_MONEY}} {{money}},00</p>
                                <p class="columnSubText">{{translate.TR_MONEY_AVAILABLE}}</p>
                            </div>
                        </div>
                        <div class="line">
                            <div class="inputValue">
                                <input type="text" :placeholder="translate.TR_TYPE_VALUE" v-model.number="deposit">
                                <div class="button">
                                    <div class="buttonText" @click="DepositMoney">
                                        <p>{{translate.TR_DEPOSIT}}</p>
                                        <!-- <i class="fa-solid fa-check"></i> -->
                                    </div>
                                </div>
                            </div>
                            <div class="inputValue">
                                <input type="text" :placeholder="translate.TR_TYPE_VALUE" v-model.number="withdraw">
                                <div class="button">
                                    <div class="buttonText" @click="WithdrawMoney">
                                        <p>{{translate.TR_WITHDRAW}}</p>
                                        <!-- <i class="fa-solid fa-check"></i> -->
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
			</div>
		</section>
	</div>
</template>

<script>
	import axios from "axios";
    import Loading from "@/components/Loading.vue";

	export default {
		name: "TemplateOneItems",

        components: {
            Loading
        },

		data: function () {
			return {
                id: this.$store.state.openned_store.id,
				money: 5,
				deposit: "",
				withdraw: "",

				is_loading: false,

				translate: this.$store.state.translate,
			};
		},

		methods: {
            DepositMoney: function(){
                this.SendToClient("DepositMoney", {
                    id: this.id,
                    value: this.deposit,
                });
                this.is_loading = true;
            },
            WithdrawMoney: function(){
                this.SendToClient("WithdrawMoney", {
                    id: this.id,
                    value: this.withdraw,
                });
                this.is_loading = true;
            },
			ValidateInputs: function () {
				if (Number.isNaN(this.deposit) || this.deposit < 0) {
					this.deposit = 0;
				}
				if (Number.isNaN(this.withdraw) || this.withdraw < 0) {
					this.withdraw = 0;
				}

				this.deposit = Math.floor(this.deposit);
				this.withdraw = Math.floor(this.withdraw);
			},
			SendToClient: function (to, data) {
				axios
					.post(`https://${this.$store.state.name_resource}/${to}`, data)
					.catch((msg) =>
						console.log(`An error has occurred (TemplateOneBalance): ${to} : ${msg}`)
					);
			},

			ReceiveLua: function (event) {
				if (!event || !event.data) return;
				const lua = event.data;
				if (lua.receive_money) {
					this.money = lua.money;
                    this.is_loading = false;
				}
			},
		},

		created: function () {
			window.addEventListener("message", this.ReceiveLua);

			this.is_loading = true;
			this.SendToClient("GetStoreMoney", {
				id: this.$store.state.openned_store.id,
			});
		},
		unmounted: function () {
			window.removeEventListener("message", this.ReceiveLua);
		},
	};
</script>

<style scoped>
	.container {
		flex: 1;
		width: 150vh;
		margin: auto;
	}

	.items {
		margin-top: 5vh;
		margin-left: 5vh;
		width: 100%;
		overflow: hidden;
	}

	.items .box {
		width: 111vh;
		height: 70vh;

		background: var(--product_background);
		border: 1px solid var(--product_border);

		margin-bottom: 2vh;
	}

	.title {
		position: relative;
	}

	.title .borderTop {
		background: var(--product_border);
		width: 100%;
		height: 0.5vh;
	}

	.title p {
		color: white;
		font-size: 1.3vh;
		padding: 0.5vh;
		padding-left: 1vh;
		text-transform: uppercase;
	}

	.title .bgTitle {
		border-right: 5vh solid transparent;
		border-top: 2.8vh solid var(--product_border);
		width: 20vh;
		height: 0;
		position: absolute;
		top: 0.5vh;
	}

	.back {
		display: flex;
		flex-direction: row;
		align-items: center;
		color: rgba(255, 255, 255, 0.596);
		font-size: 2vh;
		padding: 2vh 1vh;
	}
	.back i {
		margin-right: 1vh;
	}

	.img {
		margin: auto;
		width: 15vh;
		height: 15vh;
		/* background: gray; */
		margin-top: 5vh;
	}

	.img img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	.text {
		/* margin-left: 1vh; */
		display: flex;
		flex-direction: column;
	}

	.text .line {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		padding: 0 1vh;
		margin-bottom: 2vh;
		margin-top: 2vh;
	}

	.text .lineCenter {
		display: flex;
		flex-direction: row;
		justify-content: center;
		margin-top: 3vh;
	}

	.text .columnText {
		font-size: 5vh;
		color: rgb(35, 233, 8);
	}

	.text .columnSubText {
		font-size: 2vh;
		color: rgba(165, 190, 158, 0.534);
	}

	.text .lineCenter .columnSubText,
	.text .lineCenter .columnText {
		text-align: center;
	}

	.text .button {
		color: var(--text_themed);
		font-size: 2vh;

		position: relative;
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: center;

		width: 100%;
		height: 4vh;
	}
	.text .button .buttonText {
		position: absolute;
