		justify-content: center;

		background: var(--themed-color);
		border-radius: 5px;
		padding: 0.8vh 3vh;
	}
	.text .button .buttonText i {
		margin-left: 1vh;
	}

	.inputValue input {
		background: rgba(255, 255, 255, 0.05);
		border: none;
		outline: none;
		font-size: 2vh;
		padding: 1vh;
		margin: auto;
		text-align: center;
		margin-bottom: 2vh;
		width: 90%;
		color: white;
		border-radius: 5px;
	}

	.inputValue {
		flex: 1;
		margin-top: 5vh;
		display: flex;
		flex-direction: column;
		align-items: center;
	}

    .loading{
        margin-bottom: 10vh;
    }
</style>