<template>
	<div class="PopUpGrade">
		<div class="PopUp">
			<div v-if="confirmation">
				<svg class="checkmark" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 52 52">
					<circle class="checkmark__circle" cx="26" cy="26" r="25" fill="none" />
					<path class="checkmark__check" fill="none" d="M14.1 27.2l7.1 7.2 16.7-16.8" />
				</svg>
			</div>

			<div v-if="loading" class="loading lg-column lg-expand lg-justifyCenter lg-alignCenter">
				<Loading color_primary="rgba(145, 14, 14, 1)" color_secondary="rgba(255, 255, 255, 0.8)" />
			</div>

			<div v-if="error" class="error lg-column lg-expand lg-justifyCenter lg-alignCenter">
				<i class="fa-solid fa-xmark"></i>
			</div>

			<div v-if="quest" class="quest lg-column lg-expand lg-justifyCenter lg-alignCenter">
				<i class="fa-regular fa-circle-question"></i>
			</div>

            <div v-if="image" class="image lg-column lg-expand lg-justifyCenter lg-alignCenter">
				<img :src="img_url" alt="">
			</div>

			<p class="text">{{text}}</p>
            <p class="subtext" v-for="(subtext, index) in subtexts" :key="`sub_${index}`" >{{subtext}}</p>

			<div class="buttons field lg-line lg-justifyCenter" v-if="field">
				<input type="text" :placeholder="translate.TR_TYPE_HERE" v-model="input_text" @input="PopUpInput">
			</div>

			<div class="buttons lg-line lg-justifyCenter" v-if="buttons">
				<p class="button" @click="() => func_cancel()">{{translate.TR_CANCEL}}</p>
				<p class="button" @click="() => func_confirm(input_text)">{{translate.TR_CONFIRM}}</p>
			</div>
		</div>
	</div>
</template>

<script>
	import Loading from "@/components/Loading.vue";
	export default {
		name: "PopUp",
		components: { Loading },
		props: {
            text: { default: "" },
            subtexts: { default: []},
			buttons: { default: true },
			field: { default: true },
			confirmation: { default: false },
			loading: { default: false },
			error: { default: false },
			quest: { default: false },
            image: { default: false },
            img_url: {},
			func_confirm: {},
			func_cancel: {},
            func_input: { default: null},
		},

		data: function () {
			return {
				input_text: "",
				translate: this.$store.state.translate,
			};
		},

        methods: {
            PopUpInput: function(){
                if(this.func_input){
                    this.func_input(this.input_text, (value) => {
                        this.input_text = value;
                    });
                }
            }
        }
	};
</script>

<style scoped>
    .PopUpGrade{
        width: 100vw;
        height: 100vh;
        position: absolute;
    }
	.PopUp {
		width: 70vh;
		min-height: 30vh;
		background: rgba(0, 0, 0, 0.7);

		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		z-index: 2;

		border: 1px solid rgba(255, 255, 255, 0.4);
		border-radius: 5px;

		overflow: hidden;

		animation: popup-start 1s;
	}

	@keyframes popup-start {
		0% {
			transform: translate(-50%, 200%);
		}
		100% {
			transform: translate(-50%, -50%);
		}
	}

	.text {
		color: white;
		font-size: 2vh;
		text-align: center;
		margin-top: 2vh;
        padding: 0 2vh;
	}
    .subtext {
		color: white;
		font-size: 2vh;
		text-align: center;
		margin-top: 1vh;
        padding: 0 2vh;
	}

	.buttons {
		margin-top: 2vh;
		margin-bottom: 2vh;
	}

	.button {
		color: white;
		margin: 1vh;
		padding: 1vh 2vh;
		background: rgba(255, 255, 255, 0.1);
		border-radius: 3px;
	}

    .button:hover{
        background: rgba(145, 14, 14, 1);
        transition: 0.3 all;
    }

	.field input {
		width: 80%;
		background: rgba(255, 255, 255, 0.1);
		border: none;
		outline: none;
		font-size: 2vh;
		padding: 1vh;
		text-align: center;
		text-transform: uppercase;
		color: white;
	}

	.loading {
		margin-top: 5vh;
	}

	.error {
		margin-top: 5vh;
		color: rgb(209, 0, 0);
		font-size: 12vh;
	}

	.quest {
		margin-top: 5vh;
		color: rgb(230, 230, 230);
		font-size: 12vh;
	}

    .image{
        margin-top: 5vh;
    }

    img{
        width: 10vh;
        height: 10vh;
        object-fit: cover;
    }
</style>