<template>
	<div class="container lg-line lg-alignCenter">
		<section class="items">
            <div class="cartTitle">
				<p>{{translate.TR_NAME_STORE}}</p>
			</div>

			<div class="box">
                <div class="lg-column lg-expand lg-alignCenter">
                    <div class="img">
                        <img src="img/renomear.png" alt="">
                    </div>
                    <p class="text" >{{translate.TR_NAME_TEXT}}</p>
                </div>

				<div class="lg-line lg-expand">
                    <div class="lg-column lg-expand">
                        <input class="inputValue lg-expand" type="text" :placeholder="translate.TR_TYPE_HERE" v-model="name" >
                        
                        <p class="button lg-button-transparent lg-text-white" @click="Confirm" >{{translate.TR_CONFIRM}} </p>
                    </div>
                </div>
			</div>
		</section>
	</div>
</template>

<script>
    import axios from "axios";

	export default {
		name: "TemplateOneItems",

		data: function () {
			return {
                id: this.$store.state.openned_store.id,
                name: this.$store.state.openned_store.name,
                translate: this.$store.state.translate,
            };
		},

		methods: {
            Confirm: function(){
                this.$store.state.openned_store.name = this.name;
                this.SendToClient("ChangeStoreName", {
                    id: this.id,
                    name: this.name,
                })

            },
            SendToClient: function (to, data) {
				axios
					.post(`https://${this.$store.state.name_resource}/${to}`, data)
					.catch((msg) =>
						console.log(`An error has occurred (TemplateTwoName): ${to} : ${msg}`)
					);
			},
        },
	};
</script>

<style scoped>
	.container {
		background-color: var(--nav_border_selected);
		margin-top: -0.1vh;
		flex: 1;
		padding: 2vh 5vh;
	}

    .cartTitle {
		text-align: center;
		font-size: 5vh;
		font-weight: bolder;
		color: var(--nav_border);
	}

	.items {
		/* margin-top: 5vh; */
		margin-left: 5vh;
		width: 100%;
		overflow: hidden;

        /* background: whitesmoke; */
	}

	.items .box {
		width: 111vh;
		height: 45vh;
        margin: auto;
        margin-top: 5vh;
	}

    .img{
        width: 10vh;
        height: 10vh;
        
    }

    .img img{
        width: 100%;
        object-fit: cover;
    }

    .text{
        margin-bottom: 3vh;
        color: rgba(19, 19, 19, 0.753);
        font-size: 1.8vh;
    }

    .inputValue{
        background: rgb(255, 255, 255);
        border: 1px solid rgba(128, 128, 128, 0.2);
        outline: none;
        font-size: 2vh;
        padding: 1vh;
        margin: auto;
        text-align: center;
        margin-bottom: 2vh;
        width: 90%;
        color: rgb(122, 122, 122);
        border-radius: 5px;
    }

    .button{
        margin: auto;
        background: var(--themed-color);
        font-size: 2vh;
        color: var(--text_themed);
    }
</style>